import React, { useEffect, useState } from 'react';
import { Rating } from 'react-simple-star-rating';
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import per2 from '../../images/p3.png';
import f2 from '../../images/b2.png';
import { decrement, fetchProductByIdAsync, increment, productDetails } from './productSlice';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';
import { addToCartAsync, selectCartItem } from '../cart/cartSlice';
import { selectUserInfo } from '../user/userSlice';
import { toast } from 'react-toastify';
import { addToWishAsync, deleteFromWishAsync, fetchWishByUserAsync, selectWishItem } from '../Wishlist/wishSlice';
import { useForm } from 'react-hook-form';
import { addreviewAsync, fetchUserReviewByIdAsync, selectReviews } from '../review/reviewSlice';

const ProductDetails = () => {
    const { register, handleSubmit, formState: { errors }, reset } = useForm();
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const { id } = useParams();
    const [allsizes, setAllSizes] = useState();
    const [qty, setQty] = useState(1);
    const [wishlistdata, setWishlistdata] = useState();
    const productData = useSelector(productDetails);
    const cartdata = useSelector(selectCartItem);
    const WishItem = useSelector(selectWishItem);
    const user = useSelector(selectUserInfo);

    console.log(user);


    const reviewdata = useSelector(selectReviews)
    console.log(reviewdata);

    const [rating, setRating] = useState(0);

    const [isReviewVisible, setIsReviewVisible] = useState(false);

    const onSubmit = (data) => {
        setIsReviewVisible(false);
        const item = { ...data, email: user.email, rating: rating, productId: productData.id }
        console.log(item);

        dispatch(addreviewAsync(item))
        reset();
    };

    const toggleReviewForm = () => {
        if (!user) {
            navigate('/login');
        } else {
            setIsReviewVisible(!isReviewVisible);
        }

    };

    const handleRating = (rate) => {
        setRating(rate);
    };

    useEffect(() => {
        dispatch(fetchProductByIdAsync(id));
        dispatch(fetchUserReviewByIdAsync(id))
        if (user) dispatch(fetchWishByUserAsync(user));

        axios.get("http://localhost:8000/size")
            .then((response) => setAllSizes(response.data))
            .catch((error) => console.log(error));
    }, [dispatch, id, user, wishlistdata, isReviewVisible]);

    const isProductInWishlist = (productId) => {
        return WishItem.some(item => item.product.id === productId);
    };

    const [sizes, setSizes] = useState(1);
    const [selsizes, setSelSizes] = useState(null);
    const [activeSize, setActiveSize] = useState(null);


    const handleSize = (sizes) => {
        setSelSizes(sizes);
        var size = sizes.slice(0, -2);
        size = size / 100;
        setSizes(size);
        setActiveSize(sizes);
    };

    const handelCart = () => {
        if (!selsizes) {
            toast.error('Select size')
            return;
        }
        if (!user) {
            navigate('/login');
        } else {
            if (cartdata.findIndex((item) => item.product.id === productData.id) < 0) {
                const newItem = {
                    product: productData.id,
                    quantity: qty,
                    user: user.id,
                    sizes: selsizes,
                    price: sizes * productData.price
                };
                dispatch(addToCartAsync(newItem));
                toast.success("Item Added");
            } else {
                toast.error("Item Already added");
            }
        }
    };

    const minus = () => {
        if (qty > 1) {
            setQty(prevQty => prevQty - 1);
            dispatch(decrement({ id, qty }));
        }
    };

    const plus = () => {
        setQty(prevQty => prevQty + 1);
        dispatch(increment({ id, qty }));
    };

    const handelWish = (data) => {
        if (!user) {
            navigate('/login');
        } else {
            const newItem = { product: data.id, user: user.id };
            if (isProductInWishlist(data.id)) {
                const wishItem = WishItem.find(item => item.product.id === data.id);
                dispatch(deleteFromWishAsync(wishItem.id));
            } else {
                dispatch(addToWishAsync(newItem));
                setWishlistdata(newItem);
            }
        }
    };



    if (Array.isArray(reviewdata)) {
        var totalReview = 0;
        reviewdata.map((item) => {
            totalReview += item.rating;
        });
        console.log(totalReview / reviewdata.length);
    }



    return (
        <>
            <div className='container py-4 mt-5'>
                <div className='row pt-5'>
                    <div className='col-lg-6'>
                        <div className='roundcircle position-relative d-flex align-items-center justify-content-center pt-5'>
                            <img src={f2} alt="" className='borderclr position-absolute' />
                            <img
                                src={productData.thumbnail ? require(`../../uploads/${productData.thumbnail}`) : per2}
                                alt="Product"
                                className='z-1 img-fluid'
                                width={400}
                            />
                        </div>
                    </div>
                    <div className='col-lg-6 font pt-3 text-center text-lg-start px-4'>
                        <h1 className='detailfont'>{productData.title}</h1>
                        <p className="fw-semibold fs-5 extradetail ">{productData.brand}</p>
                        <Rating
                            onClick={handleRating}
                            initialValue={totalReview / reviewdata.length}
                            readonly={true}
                            allowFraction={true}
                            size={30}
                            fillColor='rgb(233, 202, 81)'
                            emptyColor='rgb(203, 202, 198)'
                        />
                        <hr className='w-lg-25' />
                        <p className='text-secondary py-2'>{productData.description}</p>
                        <p className="fw-semibold fs-5 extradetail ">Bottle Size</p>
                        <div className='row'>
                            {allsizes && allsizes.map((size, index) => (
                                <div className="col-lg-1 me-4 pb-2 d-grid" key={index}>
                                    <button
                                        key={index}
                                        className={`btn sizebtn ${activeSize === size.value ? "active" : ""}`}
                                        onClick={() => handleSize(size.value)}
                                    >
                                        {size.value}
                                    </button>
                                </div>
                            ))}
                        </div>
                        <div className='d-flex align-items-center py-3 justify-content-center justify-content-lg-start'>
                            <p className="fw-semibold fs-5 extradetail pt-3 pe-3">Quantity :</p>
                            <div>
                                <Link onClick={minus} className='pe-3 fs-3 qty'><i className="bi bi-patch-minus"></i></Link>
                                <span className='fs-4 text-secondary'>{qty}</span>
                                <Link onClick={plus} className='px-3 fs-3 qty'><i className="bi bi-patch-plus"></i></Link>
                            </div>
                        </div>
                        <div>
                            <p className="fw-semibold fs-4 price">Price : {(sizes) * productData.price * qty} Rs.</p>
                        </div>
                        <div className='row'>
                            <div className="col-lg-5 text-center d-grid py-2 productbtn">
                                <button className='btn myfont fw-bold mybtn btnoutlined' onClick={handelCart}>
                                    <span className='px-2'><i className="fa-solid fa-bag-shopping"></i></span>
                                    Add to cart
                                </button>
                            </div>
                            <div className="col-lg-5 text-center d-grid py-2 productbtn">
                                <button
                                    className="btn myfont fw-bold mybtn btnoutlined"
                                    onClick={toggleReviewForm}
                                >
                                    <span className="px-2">
                                        <i className="fa-solid fa-comment"></i>
                                    </span>
                                    Reviews
                                </button>
                            </div>



                            <div className="col-lg-1  text-center py-2 ">
                                <button className='btn myfont fw-bold heartdetail py-2 px-3 '>
                                    <div className='fs-4 wish' onClick={() => handelWish(productData)}>
                                        {isProductInWishlist(productData.id) ? (
                                            <i className="fa-solid fa-heart"></i>
                                        ) : (
                                            <i className="fa-regular fa-heart"></i>
                                        )}
                                    </div>
                                </button>
                            </div>
                        </div>
                    </div>
                </div>

                {isReviewVisible && (
                    <div className='row py-5'>
                        <div className='col-lg-5 m-auto py-4 px-5 rounded rounded-5 shadow-lg'>
                            <div>
                                <h5 className='footer'><i className="fa-solid fa-comments"></i>Feedback</h5>
                                <hr />
                                <div className='text-center'>
                                    <h2 className='pt-4 tblhead fw-bold'>How are you feeling?</h2>
                                    <p className=' px-5 pb-4 text-secondary'>Your input is variable in helping us better understand your needs and tailor our service accordingly.</p>
                                    <Rating
                                        onClick={handleRating}
                                        initialValue={rating}
                                        allowFraction={true}
                                        size={35}
                                        fillColor='rgb(233, 202, 81)'
                                        emptyColor='rgb(203, 202, 198)'
                                    />
                                </div>
                                <form onSubmit={handleSubmit(onSubmit)}>
                                    <div className="my-5">
                                        <textarea
                                            className="form-control border border-4 rounded rounded-5 p-3"
                                            id="exampleFormControlTextarea1"
                                            rows="4"
                                            placeholder="Add a review...."
                                            {...register('review', { required: 'Review is required' })}
                                        ></textarea>

                                        {errors.review && (
                                            <span className="text-danger pt-2">{errors.review.message}</span>
                                        )}
                                    </div>

                                    <div className="d-grid">
                                        <button type="submit" className="btn pcard fw-bold py-2 rounded-4">
                                            Submit
                                        </button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                )}

            </div>

            {/* Multiple Carousel for Reviews */}
            <div className='container-fluid '>

                {/* <Carousel
                    responsive={responsive}
                    arrows={false}
                    autoPlay={true}
                    // autoPlaySpeed={0}
                    centerMode={false}
                    customTransition="all 8s linear"
                    dotListClass=""
                    draggable
                    focusOnSelect={false}
                    infinite={true}
                    itemClass=""
                    keyBoardControl
                    // minimumTouchDrag={80}
                    pauseOnHover

                > */}
                <Carousel
                    arrows={false}
                    autoPlay={true}
                    autoPlaySpeed={0} // important for continuous flow
                    infinite={true}
                    keyBoardControl={false}
                    minimumTouchDrag={0}
                    pauseOnHover={false}
                    rewind={false}
                    rewindWithAnimation={false}
                    rtl={false}
                    shouldResetAutoplay={false}
                    showDots={false}
                    slidesToSlide={1}
                    swipeable={false}
                    draggable={false}
                    centerMode={false}
                    customTransition="transform 10000ms linear" 
                    transitionDuration={10000}
                    responsive={{
                        desktop: { breakpoint: { max: 3000, min: 1024 }, items: 3 },
                        tablet: { breakpoint: { max: 1024, min: 768 }, items: 2 },
                        mobile: { breakpoint: { max: 767, min: 0 }, items: 1 },
                    }}
                >

                    {Array.isArray(reviewdata) && reviewdata.map((review, index) => (
                        <div className='reviewcarts' key={index}>
                            <div className="reviewcartsSub card border-0 shadow-lg p-2 cardbg" style={{ backgroundColor: 'white', borderRadius: '10px' }}>
                                <div className="card-body">
                                    <h6 className="card-title tblhead fw-semibold">{review.email}</h6>
                                    <p className="card-text footer">{review.review}</p>

                                    <div className='row pt-3'>
                                        <div className='col-3'>
                                            <h1 className='bg-warning text-white rounded py-3 text-center rounded-circle sizebtn'>
                                                {review.name.charAt(0).toUpperCase()}
                                            </h1>
                                        </div>
                                        <div className='col-9'>
                                            <h5 className='pt-3 fs-5 extradetail fw-bold'>{review.name}</h5>
                                            <Rating
                                                initialValue={review.rating}
                                                allowFraction={true}
                                                size={25}
                                                readonly={true}
                                                fillColor='rgb(247, 177, 73)'
                                                emptyColor='rgb(254, 254, 254)'
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                </Carousel>


            </div>
        </>
    );
};

export default ProductDetails;
