import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { addToWish, deleteFromWish, fetchWishByUser, resetWish } from './wishAPI';

const initialState = {
  wish: [],
  status: 'idle',
};

export const addToWishAsync = createAsyncThunk(
  'wish/addToWish',
  async (item) => {
    const response = await addToWish(item);
    return response.data;
  }
);
export const fetchWishByUserAsync = createAsyncThunk(
  'wish/fetchWishByUser',
  async (user) => {
    const response = await fetchWishByUser(user);
    return response.data;
  }
);

export const deleteFromWishAsync = createAsyncThunk(
  'wish/deleteFromWish',
  async (itemId) => {
    const response = await deleteFromWish(itemId);

    return response.data;
  }
);

export const resetWishAsync = createAsyncThunk(
  'wish/resetWish',
  async (user) => {
    const response = await resetWish(user);
    return response.data;
  }
);



export const cartSlice = createSlice({
  name: 'wish',
  initialState,
  reducers: {
    
    
  },
  extraReducers: (builder) => {
    builder
      .addCase(addToWishAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(addToWishAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.wish.push(action.payload);
      })
      .addCase(fetchWishByUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchWishByUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.wish = action.payload;
      })
      .addCase(deleteFromWishAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(deleteFromWishAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index = state.wish.findIndex(item => item.id === action.payload.id)
        state.wish.splice(index, 1);
      })
      .addCase(resetWishAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(resetWishAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.wish = [];
      });
  },
});



export const selectWishItem = (state) => state.wish.wish;

export default cartSlice.reducer;
