import React, { useEffect, useState } from "react";
import {
    PaymentElement,
    useStripe,
    useElements
} from "@stripe/react-stripe-js";
import { useDispatch, useSelector } from 'react-redux';
import { selectCurrentOrder, updateOrderAsync } from "../order/orderSlice";
import startflower from '../../images/tes3-removebg-preview.png'

export default function CheckoutForm() {
    const dispatch = useDispatch();
    const stripe = useStripe();
    const elements = useElements();
    const currentOrder = useSelector(selectCurrentOrder);
    console.log(currentOrder);


    const [message, setMessage] = useState(null);
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        if (!stripe) return;

        const clientSecret = new URLSearchParams(window.location.search).get(
            "payment_intent_client_secret"
        );

        if (!clientSecret) return;

        stripe.retrievePaymentIntent(clientSecret).then(({ paymentIntent }) => {
            switch (paymentIntent.status) {
                case "succeeded":
                    setMessage("Payment succeeded!");
                    break;
                case "processing":
                    setMessage("Your payment is processing.");
                    break;
                case "requires_payment_method":
                    setMessage("Your payment was not successful, please try again.");
                    break;
                default:
                    setMessage("Something went wrong.");
                    break;
            }
        });
    }, [stripe]);

    const order = {
        ...currentOrder,
        paymentStatus: 'received'
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        dispatch(updateOrderAsync(order));

        if (!stripe || !elements) return;

        setIsLoading(true);

        const { error } = await stripe.confirmPayment({
            elements,
            confirmParams: {
                return_url: `http://localhost:8000/ordersuccess/${currentOrder.id}`,
            },
        });

        if (error.type === "card_error" || error.type === "validation_error") {
            setMessage(error.message);
        } else {
            setMessage("An unexpected error occurred.");
        }

        setIsLoading(false);
    };

    const paymentElementOptions = { layout: "tabs" };

    return (
        <>
            <div className='container-fluid homebg position-relative overflow-hidden '>
                <h1 className='homefont text-center display-1 py-5'>Stripe Payment</h1>
                <img src={startflower} alt="" className='position-absolute productimg' />
            </div>
            <div className="container mt-5">
                <div className="row">
                    {/* Stripe Payment Form */}
                    <div className="col-lg-6">
                        <div className="stripe-container shadow-lg">
                            <h3 className="tblhead">Complete Your Payment</h3>
                            <form id="payment-form" onSubmit={handleSubmit}>
                                <PaymentElement id="payment-element" options={paymentElementOptions} />
                                <button disabled={isLoading || !stripe || !elements} id="submit" className="stripe-button">
                                    {isLoading ? <div className="spinner"></div> : "Pay Now"}
                                </button>
                                {message && <div id="payment-message">{message}</div>}
                            </form>
                        </div>
                    </div>
                    {/* Order Summary & Product Details */}
                    <div className="col-lg-6">
                        <div className="order-summary shadow-lg">
                            <h3 className="tblhead fw-semibold">Order Summary</h3>
                            {currentOrder?.items?.map((item, index) => (
                                <div key={index} className="order-item">
                                    <img src={require(`../../uploads/${item.thumbnail}`)} alt={item.name} className="order-img" />
                                    <div>
                                        <h5>{item.name}</h5>
                                        <p>Quantity: {item.quantity}</p>
                                        <p>Price: {item.price.toFixed(2)} Rs.</p>
                                    </div>
                                </div>
                            ))}
                            <hr />
                            <h4 className="tblhead fw-semibold">Total: {currentOrder?.totalAmount?.toFixed(2)} Rs.</h4>
                        </div>
                    </div>




                </div>
            </div>
        </>

    );
}
