

import React from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { selectUserInfo } from "../features/user/userSlice";

const Dashboard = ({ children }) => {
  const user = useSelector(selectUserInfo)
  console.log(user);

  return (
    <>
      <div className="container-fluid">
        <div className="row">
          {/* Sidebar - Visible on medium and larger screens */}
          <div className="adminbackground  col-lg-2 col-md-4 p-0 d-none d-md-block ">
            <div className=" text-white h-100 ">
              <div className="p-3">
                <h4>
                  <span className="pe-2 fs-3 text-info">
                    <i className="fa-brands fa-square-pied-piper"></i>
                  </span>
                  Data Able
                </h4>
                <hr />
              </div>
              <ul className="nav flex-column">
                <Link to={"/dashbord"} className="fw-bold text-white-50 px-3 pb-2 text-decoration-none">
                  DASHBOARD
                </Link>

                <li className="nav-item adminlink px-3 py-1">
                  <Link className="nav-link text-white px-0 font" to="/insertproduct">
                    <i className="bi bi-database-add pe-2"></i> Add Products
                  </Link>
                </li>
                <li className="nav-item adminlink px-3 py-1">
                  <Link className="nav-link text-white px-0 font" to="/manageProducts">
                    <i className="bi bi-box pe-2"></i> Manage Products
                  </Link>
                </li>
                <li className="nav-item adminlink px-3 py-1">
                  <Link className="nav-link text-white px-0 font" to="/adminorders">
                    <i className="bi bi-cart-check pe-2"></i> Orders
                  </Link>
                </li>
                <Link to={"/dashbord"} className="fw-bold text-white-50 px-3 pb-2 text-decoration-none">
                  BRANDS-SIZES
                </Link>
                <li className="nav-item adminlink px-3 py-1">
                  <Link className="nav-link text-white px-0 font" to="/addBrands">
                    <i className="bi bi-award pe-2"></i> Brand
                  </Link>
                </li>
                <li className="nav-item adminlink px-3 py-1">
                  <Link className="nav-link text-white px-0 font" to="/manageBreand">
                    <i class="fa-brands fa-wpforms pe-2"></i>   Manage Brands
                  </Link>
                </li>
                <li className="nav-item adminlink px-3 py-1">
                  <Link className="nav-link text-white px-0 font" to="/addSizes">
                    <i class="bi bi-bounding-box pe-2"></i>    Sizes
                  </Link>
                </li>
                <Link to={"/dashbord"} className="fw-bold text-white-50 px-3 pb-2 text-decoration-none">
                  USER
                </Link>
                <li className="nav-item adminlink px-3 py-1">
                  <Link className="nav-link text-white px-0 font" to="/userDetails">
                    <i class="fa-regular fa-address-card pe-2"></i>   UserList
                  </Link>
                </li>
                <li className="nav-item adminlink px-3 py-1">
                  <Link className="nav-link text-white px-0 font" to="/adminReviews">
                    <i class="fa-regular fa-paste pe-2"></i>   Reviews
                  </Link>
                </li>
                <li className="nav-item adminlink px-3 py-1">
                  <Link className="nav-link text-white px-0 font" to="/">
                  <i class="fa-solid fa-camera-rotate pe-2"></i>   User Application
                  </Link>
                </li>
                <div className="px-3">
                  <hr />
                </div>

                {user  ? (
                  <li className="nav-item adminlink px-3 py-1 mb-4">
                    <Link className="nav-link text-white px-0 font fw-semibold" to={'/logout'}>
                      <i className="fa-solid fa-sign-out-alt pe-2"></i> Logout
                    </Link>
                  </li>
                ) : (
                  <li className="nav-item adminlink px-3 py-1">
                    <Link className="nav-link text-white px-0 font fw-semibold" to={'/login'}>
                      <i className="fa-solid fa-cogs pe-2"></i> Login
                    </Link>
                  </li>
                )}
              </ul>
            </div>
          </div>

          {/* Main Content */}
          <div className="admin-main col-lg-10 col-md-8">
            {/* Mobile Navigation - Visible only on small screens */}
            <nav className="navbar navbar-expand-lg  d-block d-md-none">
              <div className="container-fluid">
                <button
                  className="navbar-toggler"
                  type="button"
                  data-bs-toggle="collapse"
                  data-bs-target="#mobileNavbar"
                  aria-controls="mobileNavbar"
                  aria-expanded="false"
                  aria-label="Toggle navigation"
                >
                  <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="mobileNavbar">
                  <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                    <li className="nav-item">
                      <Link className="nav-link" to={"/dashbord"}><i class="fa-brands fa-osi pe-2"></i> Dashboard</Link>
                    </li>
                    <li className="nav-item">
                      <Link className="nav-link" to="/insertproduct"><i className="bi bi-database-add pe-2"></i> Add Products</Link>
                    </li>
                    <li className="nav-item">
                      <Link className="nav-link" to="/manageProducts"><i className="bi bi-box pe-2"></i> Manage Products</Link>
                    </li>
                    <li className="nav-item">
                      <Link className="nav-link" to="/adminorders">  <i className="bi bi-cart-check pe-2"></i>Orders</Link>
                    </li>
                    <li className="nav-item">
                      <Link className="nav-link" to="/addBrands"><i className="bi bi-award pe-2"></i> Brand</Link>
                    </li>
                    <li className="nav-item">
                      <Link className="nav-link" to="/manageBreand"><i class="fa-brands fa-wpforms pe-2"></i> Manage Brand</Link>
                    </li>
                    <li className="nav-item">
                      <Link className="nav-link" to="/addSizes"> <i class="bi bi-bounding-box pe-2"></i>Sizes</Link>
                    </li>
                    <li className="nav-item">
                      <Link className="nav-link" to="/adminReviews"><i class="fa-regular fa-paste pe-2"></i> Rewiews</Link>
                    </li>
                    <li className="nav-item">
                      <Link className="nav-link" to="/userDetails"> <i class="fa-regular fa-address-card pe-2"></i> Userlist</Link>
                    </li>
                    <hr />
                    {user ? (
                      <li className="nav-item">
                        <Link className="nav-link fw-semibold" to="/logout">
                          <i className="fa-solid fa-sign-out-alt pe-2"></i> Logout
                        </Link>
                      </li>
                    ) : (
                      <li className="nav-item">
                        <Link className="nav-link fw-semibold" to="/login">
                          <i className="fa-solid fa-cogs pe-2"></i> Login
                        </Link>
                      </li>
                    )}
                  </ul>
                </div>
              </div>
            </nav>

            {/* Page Content */}
            <div className="container-fluid  py-3">
              {children}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Dashboard;

