import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import name1 from "../images/user.jpg";
import { activeUserAsync, fetchAllUsersAsync, selectAdminUserList } from '../features/user/userSlice'

const AdminUserDetails = () => {
    const dispatch = useDispatch()
    const userdata = useSelector(selectAdminUserList)
    const [user,setUser] = useState('')

    // Ensure userdata is an array or fallback to an empty array
    const users = Array.isArray(userdata) ? userdata : []
    console.log(users);
    
    const changechack = (e,user)=>{
        const update = {
            id:user.id,
            isactive:e.target.checked  
        }
        dispatch(activeUserAsync(update))
        setUser(update)
    }

    useEffect(() => {
        dispatch(fetchAllUsersAsync());
    }, [user]);

    return (
        <>
            <div className="py-5 admin-detail">
                <div className="container  px-5">
                    <div className="row">
                        <div className="col-lg-8">
                            <h2 className="tablecolor text-lg-start text-center">USERS INFORMATIONS...</h2>
                        </div>
                    </div>
                </div>
                <div className="container my-4 px-lg-5 table-responsive">
                    <table className="table table-borderless align-middle">
                        <thead className=" text-center">
                            <tr className="shadow rounded rounded-4">
                                <th className="tabletablehead py-4 myfont rounded-4 rounded-end-0 fw-semibold">Profile</th>
                                <th className="tabletablehead py-4 myfont fw-semibold">UserId </th>
                                <th className="tabletablehead py-4 myfont fw-semibold">UserName </th>
                                <th className="tabletablehead py-4 myfont fw-semibold">Email </th>
                                <th className="tabletablehead py-4 myfont fw-semibold">Role </th>
                                <th className="tabletablehead py-4  myfont fw-semibold rounded-4 rounded-start-0">Action</th>
                            </tr>
                        </thead>
                        <tbody className="text-center ">

                            {users.map((user, index) => (
                                <>
                                    <tr><td className='bg-transparent pb-3'></td></tr>
                                    <tr className="tableshedow  rounded rounded-4">
                                        <td className=" px-0 py-3  myfont tablecolor rounded-4 rounded-end-0">
                                            <img src={name1} className='shadow-lg rounded-5' alt="" height={80} />
                                        </td>
                                        <td className="py-4 tablecolor">{user.id}</td>
                                        <td className="py-4 tablecolor px-4">{user.name}</td>
                                        <td className="py-4 tablecolor px-4">{user.email}</td>
                                        <td className="py-4 tablecolor px-4">{user.role}</td>
                                        <td className="py-4 tablecolor  rounded-4 rounded-start-0">
                                            <div class="form-check d-flex align-items-baseline justify-content-center">
                                                <input class="form-check-input fs-5 usercheck" type="checkbox" checked={user.isactive} value="" id="flexCheckDefault" onChange={(e)=>changechack(e,user)} />
                                            </div>
                                        </td>
                                    </tr>
                                </>

                            ))}
                        </tbody>
                    </table>
                </div>
            </div>
        </>
    )
}

export default AdminUserDetails;
