import React, { useEffect } from 'react'
import logo from '../../images/logo.png'
import test from '../../images/tes3-removebg-preview.png'
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { createUserAsync, selectUserInfo } from '../user/userSlice';
import { Link, Navigate, useNavigate } from 'react-router-dom';

const SignUpPage = () => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const userinfo = useSelector(selectUserInfo)
    const {
        register,
        handleSubmit,
        reset,
        formState: { errors },

    } = useForm();
    

    return (
        <>
         {userinfo && <Navigate to={'/'} replace = {true}></Navigate>}
            <div className='container-fluid py-5 px-5 homebg '>
                <div className='container loginbg py-4 px-4 bg-white shadow-lg position-relative overflow-hidden'>
                    <div className='row'>
                        <div className='col-lg-5 m-auto '>
                            <h1 className=' pt-1 text-center'>
                                <img src={logo} alt="" height={60} />
                                <span className='loginlogo px-2'>Euphoria</span>
                            </h1>
                            <p className='text-center loginfont'>Fragrances fill the senses with the mysterious</p>
                            <h3 className='text-center loginfont pt-2 fw-semibold'>Sign Up</h3>

                            {/* signup */}
                            <form action="" onSubmit={handleSubmit((data) =>
                                dispatch(createUserAsync(
                                    {
                                        name: data.name,
                                        email: data.email,
                                        password: data.password,
                                    }
                                ))
                                
                            )} encType="multipart/form-data">

                                <div class="form-floating mb-3">
                                    <input type="text" class="form-control loginform" placeholder="username"
                                        {...register("name", { required: "Username must be required" })} />
                                    <label for="name">Username</label>
                                    {errors.name && (
                                        <span className="text-danger pt-2">Username must be required.</span>
                                    )}
                                </div>
                                <div class="form-floating  mb-3">
                                    <input type="email" class="form-control loginform" id="floatingInput" placeholder="name@example.com"
                                        {...register("email", {
                                            required: "Email must be required",
                                        })}
                                    />
                                    <label for="email">Email</label>
                                    {errors.email && (
                                        <span className="text-danger pt-2">Email must be required.</span>
                                    )}
                                </div>
                                <div class="form-floating  mb-3">
                                    <input type="password" class="form-control loginform" placeholder="Password"
                                        {...register("password", {
                                            required: "Password must be required",
                                            pattern: {
                                                value:
                                                    /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/gm,
                                                message: `- at least 8 characters\n
                                                - must contain at least 1 uppercase letter, 1 lowercase letter, and 1 number\n
                                                - Can contain special characters`,
                                            },
                                        })}
                                    />
                                    <label for="Password">Password</label>
                                    {errors.password && (
                                        <span className="text-danger pt-2">{errors.password.message}</span>
                                    )}
                                </div>
                                <div class="form-floating  mb-3">
                                    <input type="password" class="form-control loginform" placeholder="ConfirmPassword"  {...register("confirmpassword", {
                                        required: "Confirm Password must be required",
                                        validate: (value, formValues) =>
                                            value === formValues.password || 'password not matching',
                                    })} />
                                    <label for="cpassword">Confirm Password</label>
                                    {errors.confirmpassword && (
                                        <span className="text-danger pt-2">{errors.confirmpassword.message}</span>
                                    )}
                                </div>
                                <div className='row '>
                                    <div className="col-lg-12  text-lg-center py-3 homebtn d-lg-grid d-block text-end">
                                        <button className='btn myfont fw-bold mybtn btnoutlined '>Sign Up
                                            <span className='ps-1 homeicon '><i class="fa-solid fa-angle-right"></i></span>
                                        </button>
                                    </div>
                                </div>
                                <p className='text-center text-secondary'>Already have an account? <Link className='text-decoration-none qty fw-bold' to='/login'>Log In</Link></p>
                            </form>

                            <img src={test} alt="" className='position-absolute loginimg' />
                            <img src={test} alt="" className='position-absolute loginimg1' />

                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default SignUpPage
