import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { fetchLoggedInUserOrderAsync, selectOrders } from '../order/orderSlice';
import { selectUserInfo } from '../user/userSlice';
import cartflw from '../../images/flower3.png'

const TrackOrder = () => {
    const dispatch = useDispatch();
    const user = useSelector(selectUserInfo)
    const order = useSelector(selectOrders)
    const orderdata = order[0]
    console.log(orderdata);

    useEffect(() => {
        dispatch(fetchLoggedInUserOrderAsync(user.id));
    }, [dispatch]);

    return (
        <>
            <div className='container py-5'>
                {/* <p>Pending</p>
                <p>Progress</p>
                <p>Delivered</p> */}
                <div className="container-fluid pt-5 ">
                    <div className="container position-relative pt-4">
                        <h1 className="hometitle pt-3 ps-3">Track Your Order</h1>

                        {orderdata &&
                            <div className="shadow-lg rounded-4 py-5 bg-white px-5">
                                <p className="fw-semibold fs-3 tblhead ">Order Status</p>
                                <p className="fw-semibold fs-5 extradetail ">Order Number : {orderdata.id} </p>

                                <div className="py-5 d-flex align-items-center justify-content-center">
                                    {/* Order Placed */}
                                    <hr className={`orderhr ${['pending', 'progress', 'delivered'].includes(orderdata.status) ? 'active' : ''}`} />
                                    <div className="text-center">
                                        <span className={`fs-2 ordertrack py-3 px-4 rounded-circle ${['pending', 'progress', 'delivered'].includes(orderdata.status) ? 'active' : ''}`}>
                                            <i className="fa-solid fa-calendar"></i>
                                        </span>
                                        <p className="py-3 tblhead fw-bold">Order</p>
                                    </div>
                                    <hr className={`orderhr ${['pending', 'progress', 'delivered'].includes(orderdata.status) ? 'active' : ''}`} />

                                    {/* In Transit */}
                                    <hr className={`orderhr ${['progress', 'delivered'].includes(orderdata.status) ? 'active' : ''}`} />
                                    <div className="text-center">
                                        <span className={`fs-2 ordertrack py-3 px-4 rounded-circle ${['progress', 'delivered'].includes(orderdata.status) ? 'active' : ''}`}>
                                            <i className="fa-solid fa-truck-front"></i>
                                        </span>
                                        <p className="py-3 tblhead fw-bold">In Transit</p>
                                    </div>
                                    <hr className={`orderhr ${['progress', 'delivered'].includes(orderdata.status) ? 'active' : ''}`} />

                                    {/* Delivered */}
                                    <hr className={`orderhr ${orderdata.status === 'delivered' ? 'active' : ''}`} />
                                    <div className="text-center">
                                        <span className={`fs-2 ordertrack py-3 px-4 rounded-circle ${orderdata.status === 'delivered' ? 'active' : ''}`}>
                                            <i className="fa-solid fa-check"></i>
                                        </span>
                                        <p className="py-3 tblhead fw-bold">Delivered</p>
                                    </div>
                                    <hr className={`orderhr ${orderdata.status === 'delivered' ? 'active' : ''}`} />
                                </div>
                                {orderdata && orderdata.selectedAddress
                                    .map((address, index) => (
                                        <div className='p-3 rounded-2 homebg '>
                                            <p className='extradetail fw-bold fs-5 '><span className='pe-2'><i class="fa-solid fa-circle-exclamation"></i></span>Shpping Address </p>
                                            <p className='tblhead fw-semibold'>{address.fname}</p>
                                            <p className='tblhead fw-semibold'>{address.address}<br />{address.city} , {address.state}</p>
                                        </div>
                                    ))}

                            </div>
                        }

                        <img src={cartflw} alt="" className='img-fluid position-absolute profileimg' />
                    </div>
                </div>
            </div>
        </>
    )
}

export default TrackOrder
