import React, { useEffect, useState } from 'react'
import { useForm } from 'react-hook-form';
import per3 from '../../images/perfume2.png'
import { useDispatch, useSelector } from 'react-redux';
import { deleteItemFromCartAsync, selectCartItem } from '../cart/cartSlice';
import { Link, Navigate } from 'react-router-dom';
import cartflw from '../../images/flower3.png'
import { selectAddress, selectUserInfo, updateUserAsync } from '../user/userSlice';
import { toast } from 'react-toastify';
import { createOrderAsync, selectCurrentOrder } from '../order/orderSlice';


const CheckoutPage = () => {
    const {
        register,
        handleSubmit,
        reset,
        formState: { errors },

    } = useForm();
    const dispatch = useDispatch();



    const cartdata = useSelector(selectCartItem)
    const user = useSelector(selectUserInfo)
    var userId = user.id
    const [selectedAddress, setSelectedAddress] = useState(null);
    const [paymentMethod, setPaymentMethod] = useState(null);

    const address = useSelector(selectAddress)
    console.log(address);

    const currentOrder = useSelector(selectCurrentOrder)



    const handledelete = (id) => {
        dispatch(deleteItemFromCartAsync(id));
    }

    const handlecheckout = (data) => {
        dispatch(updateUserAsync({
            ...user,
            addresses: [...user.addresses, data],
            userId
        }))
        reset()
    }
    var Totalprice = 0;
    cartdata.map((item) => {
        Totalprice = Totalprice + item.price * item.quantity;
    })


    const [quantity, setQuantity] = useState(0);
    const totalQuantity = () => {
        let totalqty = 0
        cartdata.map((item) => {
            totalqty = totalqty + item.quantity;
        })
        setQuantity(totalqty)
    }
    useEffect(() => {
        totalQuantity()
    }, [cartdata])

    const handleAddress = (data) => {
        console.log(data);

        setSelectedAddress(data);

    };
    const handlePayment = (method) => {
        setPaymentMethod(method);
    };




    var newCartData = cartdata.map(item => ({
        title: item.product.title,
        description: item.product.description,
        price: item.quantity * item.price,
        rating: item.product.rating,
        brand: item.product.brand,
        thumbnail: item.product.thumbnail,
        sizes: item.sizes,
        quantity: item.quantity,

    }));

    const handleOrder = (e) => {
        if (selectedAddress !== null && paymentMethod) {
            const order = {
                items: newCartData,
                totalAmount: Totalprice,
                totalItems: quantity,
                user: user.id,
                paymentMethod: paymentMethod,
                selectedAddress: [selectedAddress],
                status: 'pending',
            };
            dispatch(createOrderAsync(order));
        } else {

            toast.error('Enter Address and Payment method');
        }
    };



    return (
        <>
            {!cartdata.length && <Navigate to={'/'} replace={true}></Navigate>}
            {currentOrder && currentOrder.paymentMethod === 'Cash' && (
                <Navigate
                    to={`/ordersuccess/${currentOrder.id}`}
                    replace={true}
                ></Navigate>
            )}
            {currentOrder && currentOrder.paymentMethod === 'Card' && (
                <Navigate to={`/stripe-checkout`} replace={true}></Navigate>
            )}

            <div className='container-fluid pt-4'>
                <div className='container position-relative pt-5 mt-5'>
                    <div className='row'>
                        <div className='col-lg-7 pe-lg-5 px-4 px-lg-0'>
                            <h2 className='hometitle text-lg-start text-center pb-4 pt-2'>Checkout</h2>
                            <form onSubmit={handleSubmit((data) => handlecheckout(data))}
                                encType="multipart/form-data"
                            >
                                <div className='pb-3'>
                                    <label for="title" className="form-label tblhead">
                                        Full Name
                                    </label>
                                    <input
                                        type="text"
                                        className="form-control shadow"
                                        {...register("fname", { required: "fname must be required" })}
                                    />
                                    {errors.fname && (
                                        <p className="text-danger pt-2">First Name must be required.</p>
                                    )}
                                </div>
                                <div className='pb-3'>
                                    <label for="title" className="form-label tblhead ">
                                        Email Address
                                    </label>
                                    <input
                                        type="email"
                                        className="form-control shadow"
                                        {...register("email", { required: "email must be required" })}
                                    />
                                    {errors.email && (
                                        <p className="text-danger pt-2">Email must be required.</p>
                                    )}
                                </div>
                                <div className='pb-3'>
                                    <label for="title" className="form-label tblhead ">
                                        Phno
                                    </label>
                                    <input
                                        type="text"
                                        className="form-control shadow"
                                        {...register("Phno", { required: "Phno must be required" })}
                                    />
                                    {errors.Phno && (
                                        <p className="text-danger pt-2">Phone number must be required.</p>
                                    )}
                                </div>
                                <div className='pb-3'>
                                    <label for="title" className="form-label tblhead ">
                                        Street address
                                    </label>
                                    <input
                                        type="text"
                                        className="form-control shadow"
                                        {...register("address", { required: "address must be required" })}
                                    />
                                    {errors.address && (
                                        <p className="text-danger pt-2">Address number must be required.</p>
                                    )}
                                </div>
                                <div className='row pb-3'>
                                    <div className='col-lg-4'>
                                        <label for="city" className="form-label tblhead ">
                                            City
                                        </label>
                                        <input
                                            type="text"
                                            className="form-control shadow"
                                            {...register("city", { required: "city must be required" })}
                                        />
                                        {errors.city && (
                                            <p className="text-danger pt-2">City number must be required.</p>
                                        )}
                                    </div>
                                    <div className='col-lg-4'>
                                        <label for="state" className="form-label tblhead ">
                                            State / Province
                                        </label>
                                        <input
                                            type="text"
                                            className="form-control shadow"
                                            {...register("state", { required: "state must be required" })}
                                        />
                                        {errors.state && (
                                            <p className="text-danger pt-2">State must be required.</p>
                                        )}
                                    </div>
                                    <div className='col-lg-4'>
                                        <label for="pinCode" className="form-label tblhead">
                                            ZIP / Postal code
                                        </label>
                                        <input
                                            type="pinCode"
                                            className="form-control shadow"
                                            {...register("pinCode", { required: "pinCode must be required" })}
                                        />
                                        {errors.pinCode && (
                                            <p className="text-danger pt-2">Pincode must be required.</p>
                                        )}
                                    </div>
                                </div>

                                <div className=" text-center d-grid py-3  homebtn">
                                    <button className='btn myfont fw-bold mybtn btnoutlined text-white '>
                                        Add Address
                                        <span className='ps-1 homeicon '><i class="fa-solid fa-angle-right"></i></span>
                                    </button>
                                </div>

                                <img src={cartflw} alt="" className='img-fluid position-absolute checkimg' />
                            </form>
                            <p className="fw-semibold fs-5 extradetail pt-4">Your Addresses</p>
                            {address.map((data) =>
                                <div className='border px-4 pt-3 my-3  rounded-4 border-2'>
                                    <div class="form-check">
                                        <input class="form-check-input" type="radio" name="flexRadioDefault" id="flexRadioDefault1" onChange={() => handleAddress(data)} />
                                        <p className='tblhead fw-bold'>{data.fname}</p>
                                        <div className='d-flex justify-content-between'>
                                            <p className='text-secondary'>{data.address}</p>
                                            <p className='tblhead'>{data.pinCode}</p>
                                        </div>
                                        <div className='d-flex justify-content-between'>
                                            <p className='text-secondary'>Phno : {data.Phno}</p>
                                            <p className='tblhead'>{data.city}</p>
                                        </div>
                                    </div>
                                </div>
                            )}


                            <p className="fw-semibold fs-5 extradetail pt-4">Payment Method</p>
                            <div className='row pb-5'>
                                <div className='col-6 d-grid'>
                                    <button class=" btn  pcard fw-bold py-2 rounded-3"
                                        onClick={() => handlePayment('Cash')}>
                                        <span className='px-2'><i class="fa-solid fa-wallet"></i></span>
                                        Cash Payments
                                    </button>
                                </div>
                                <div className='col-6 d-grid'>
                                    <button class=" btn  pcard fw-bold py-2 rounded-3"
                                        onClick={() => handlePayment('Card')}>
                                        <span className='px-2'><i class="fa-solid fa-credit-card"></i></span>
                                        Card Payments
                                    </button>
                                </div>
                            </div>
                        </div>


                        <div className='col-lg-5'>
                            <h4 className='extradetail pt-5 pb-4 fw-semibold'>
                                <span className='pe-3'><i class="fa-brands fa-skyatlas"></i></span>Review your cart</h4>
                            {cartdata.map((data) =>
                                <div className='row py-2'>

                                    <div className='col-2 border p-2 rounded-3 d-flex align-items-center justify-content-center'>
                                        <img src={require(`../../uploads/${data.product.thumbnail}`)} alt={data.product.title} className="" height={80} />
                                    </div>

                                    <div className='col-8 ps-4'>
                                        <p className='tblhead pt-2'>{data.product.title} [{data.quantity}]</p>
                                        <div className='d-flex p-0'>
                                            <p className='fw-bold tblhead pe-5'>{data.quantity * data.price} Rs.</p>
                                            <p className='text-secondary'>{data.sizes[0]} </p>
                                        </div>

                                    </div>
                                    <div className='col-2'>
                                        <button className='btn border border-2 mt-3 rounded-circle text-danger'><i class="fa-solid fa-xmark" onClick={() => handledelete(data.id)}></i></button>
                                    </div>
                                </div>
                            )}
                            <div className='homebg my-5 py-4 px-5 rounded rounded-4'>
                                <h3 className='extradetail py-3'>Order Summary</h3>
                                <div className='d-flex justify-content-between fs-5 tblhead'>
                                    <p>Subtotal</p>
                                    <p>{Math.ceil(Totalprice)} Rs.</p>
                                </div>
                                <div className='d-flex justify-content-between fs-5 tblhead'>
                                    <p>Total Items</p>
                                    <p>{quantity} Items</p>
                                </div>
                                <hr />
                                <div className='d-flex justify-content-between fs-5 tblhead'>
                                    <p>Total</p>
                                    <p>{Math.ceil(Totalprice)} Rs.</p>
                                </div>
                                <div className=" text-center d-grid py-3  homebtn">
                                    <button onClick={handleOrder} className='btn myfont fw-bold mybtn btnoutlined text-white '>
                                        Order Now
                                        <span className='ps-1 homeicon '><i class="fa-solid fa-angle-right"></i></span>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}

export default CheckoutPage
