import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { selectUserInfo } from '../user/userSlice';
import { fetchLoggedInUserOrderAsync, selectOrders } from '../order/orderSlice';
import test from '../../images/tes3-removebg-preview.png'

const UserOrders = () => {
    const dispatch = useDispatch();
    const user = useSelector(selectUserInfo)
    const order = useSelector(selectOrders)
    console.log(order);
    



    useEffect(() => {
        dispatch(fetchLoggedInUserOrderAsync(user.id));
    }, [dispatch]);
    return (
        <>
            <div className='container-fluid homebg pb-5 position-relative overflow-hidden '>
                <div className='container '>
                    <h1 className='hometitle pt-3 ps-3'>User Orders</h1>
                    {order.map((orderdata) =>
                        <div className='shadow-lg rounded-4 py-5 bg-white my-5'>
                            <p className=' tblhead fw-bold h3 ps-5'>Order : {orderdata.id}</p>
                            <p className='fw-semibold footer ps-5'>Order Status : {orderdata.status}</p>
                            {orderdata.items.map((data) => (
                                <div className='row py-2'>
                                    <div className='col-lg-2 d-flex justify-content-center'>
                                        <img src={require(`../../uploads/${data.thumbnail}`)} alt="" width={150} className='border px-4 py-1 border-2 rounded-4' />
                                    </div>

                                    <div className='col-lg-10 pe-5'>
                                        <div className='d-flex justify-content-between'>
                                            <div>
                                                <p className='extradetail fw-semibold fs-5 newlh pt-2'>{data.title}</p>
                                                <p className='tblhead fw-semibold newlh'>{data.brand}</p>
                                            </div>
                                            <div className='tblhead fw-bold fs-5 pt-1'>Price : {data.price} Rs.</div>
                                        </div>
                                        <p className='footer text-secondary '>Size: {data.sizes}</p>
                                        <p className='extradetail newlh'>Quantity : {data.quantity}</p>
                                    </div>

                                </div>
                            ))}
                            <hr className='me-5 ms-4' />
                            <div className='d-flex justify-content-between px-5'>
                                <p className='extradetail fw-semibold fs-5 newlh pt-2'>SubTotal : </p>
                                <p className='tblhead fw-bold fs-5'>{orderdata.totalAmount} Rs.</p>
                            </div>
                            <p className="fw-semibold fs-5 extradetail px-5">Your Addresses</p>
                            {orderdata.selectedAddress.map((data) => (
                                <div className=' px-5  '>
                                    <div className='border rounded-4 border-2 px-3 pt-3'>
                                        <p className='fw-semibold tblhead'>{data.fname}</p>

                                        <div className='d-flex justify-content-between'>
                                            <p className='text-secondary'>{data.address}</p>
                                            <p className='tblhead'>{data.pinCode}</p>
                                        </div>
                                        <div className='d-flex justify-content-between'>
                                            <p className='text-secondary'>Phno : {data.Phno}</p>
                                            <p className='tblhead'>{data.city}</p>
                                        </div>
                                    </div>

                                </div>
                            ))}


                        </div>
                    )}

                </div>
                <img src={test} alt="" className='position-absolute orderimg' />
            </div>

        </>
    )
}

export default UserOrders
