
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import per from "../images/perfume2.png";
import { useDispatch, useSelector } from "react-redux";
import { fetchAllOrdersAsync, selectOrders, updateOrderAsync } from "../features/order/orderSlice";


const CompleteOrder = () => {
  const dispatch = useDispatch()
  const [editableOrderId, setEditableOrderId] = useState(-1);
  var orderdata = useSelector(selectOrders)
  console.log(orderdata);

  useEffect(() => {
    dispatch(fetchAllOrdersAsync());
  }, []);
  const handleEdit = (order) => {
    setEditableOrderId(order.id);
  };

  const chooseColor = (status) => {
    switch (status) {
      case 'delivered':
        return 'bg-success text-white bg-opacity-75';
      case 'cancelled':
        return 'bg-danger text-white bg-opacity-75';
      default:
        return 'bg-primary text-white bg-opacity-75';
    }
  };

  var orderdata = orderdata.filter(ord => ord.status === 'delivered' || ord.status === 'cancelled')



  const handleOrderStatus = (e, order) => {
    const updatedOrder = { ...order, status: e.target.value };
    dispatch(updateOrderAsync(updatedOrder));
    setEditableOrderId(-1);
  };


  return (
    <div className="py-5 admin-detail">
      <div className="container  px-5">
        <div className="row">
          <div className="col-lg-8">
            <h2 className="tablecolor text-lg-start text-center">COMPLETE ORDERS...</h2>
          </div>
          <div className="col-lg-4">
            <div className="text-lg-end text-center">
              <Link
                className="mybtn btnoutlined"
                to="/adminorders"
              >
                Order
              </Link>
            </div>
          </div>
        </div>

      </div>
      <div className="container my-4 px-lg-5 table-responsive">
        <table className="table table-borderless align-middle">
          <thead className=" text-center">
            <tr className="shadow rounded rounded-4">
              <th className="tabletablehead py-4 myfont rounded-4 rounded-end-0 fw-semibold">Order</th>
              <th className="tabletablehead py-4 myfont fw-semibold">Items</th>
              <th className="tabletablehead py-4 myfont fw-semibold">Total </th>
              <th className="tabletablehead py-4 myfont fw-semibold">Shipping Address</th>
              <th className="tabletablehead py-4 myfont fw-semibold">Payment</th>
              <th className="tabletablehead py-4 myfont fw-semibold rounded-4 rounded-start-0">PaymentStatus</th>

            </tr>
          </thead>
          <tbody className=" text-center">

            <>
              {orderdata.map((order, index) => (
                <>

                  <tr><td className='bg-transparent pb-3'></td></tr>
                  <tr tr className="tableshedow  rounded rounded-4 ">
                    <td className="py-4 px-0 tablecolor rounded-4 rounded-end-0">{index}</td>

                    <td className="py-4 tablecolor">
                      {order.items.map((item, itemIndex) => (
                        <div className="d-flex align-items-center">
                          <div key={itemIndex}>
                            <img
                              src={require(`../uploads/${item.thumbnail}`)}
                              alt={item.title}
                              className="rounded-3"
                              width={60}
                              height={60}
                            />
                          </div>
                          <div>
                            {item.title} - #{item.quantity} -[{item.sizes}] - {item.price} Rs.
                          </div>
                        </div>

                      ))}
                    </td>
                    <td className="py-4 tablecolor">{order.totalAmount} Rs.</td>
                    {order.selectedAddress.map((address) => (
                      <td className="py-4 tablecolor">
                        <div>{address.address}</div>
                        <div className="fw-semibold">{address.fname}</div>
                      </td>
                    ))}
                    <td className="py-4 tablecolor">{order.paymentMethod}</td>
                    <td className="py-4 tablecolor">
                      <span
                        className={`${chooseColor(
                          order.status
                        )} py-1 px-3 rounded-3`}
                      >
                        {order.status}
                      </span>
                    </td>


                  </tr>
                </>

              ))}
            </>
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default CompleteOrder;

