import React from "react";
import { Link } from "react-router-dom";

const AdminHome = () => {
  return (
    <div className="py-5 ">
      <h4 className="px-4 color ">Dashboard</h4>
      <div className="container">
        <div className="row">
          <div className="col-lg-4 px-4 py-3">
            <Link to="/insertproduct" className=" text-decoration-none h5 color">
              <div
                className="adminmenu1 py-5 text-center"
              >
                <span className="pe-2">
                  <i className="bi bi-database-add"></i>
                </span>
                ADD PRODUCTS
              </div>
            </Link>
          </div>
          <div className="col-lg-4 px-4 py-3">
            <Link className="text-decoration-none h5 color" to="/manageProducts">
              <div className="adminmenu2 py-5 text-center ">
                <span className="pe-2">
                  <i className="bi bi-box"></i>
                </span>
                MANAGE PRODUCTS
              </div>
            </Link>
          </div>
          <div className="col-lg-4 px-4 py-3">
            <Link className=" text-decoration-none h5 color" to="/adminorders">
              <div className="adminmenu3 py-5 text-center">
                <span className="pe-2">
                  <i className="bi bi-bag-check"></i>
                </span>
                OUR ORDERS
              </div>
            </Link>
          </div>
        </div>
        {/* circle design */}
        <div className="row">
          <div className="col-lg-4 p-lg-5 my-4 d-flex align-items-center justify-content-center justify-content-lg-start ">
            <Link to={"/userDetails"} className="text-decoration-none admincircle1 border rounded-circle d-flex align-items-center justify-content-center position-relative">
              <div className="circle1 text-center d-flex align-items-center justify-content-center position-absolute fw-semibold shadow">
                USER INFORMATIONS
              </div>
            </Link>

          </div>
          <div className="col-lg-4"></div>
          <div className="col-lg-4 p-lg-5 my-4  d-flex align-items-center justify-content-center justify-content-lg-start ">
            <Link to={"/adminReviews"} className="text-decoration-none admincircle2 border rounded-circle d-flex align-items-center justify-content-center position-relative">
              <div className="circle2 text-center d-flex align-items-center justify-content-center position-absolute fw-semibold shadow">
                REVIEWS DATA
              </div>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminHome;
