
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";

const AdminProductUpdate = () => {
  const navigate = useNavigate()

  const { id } = useParams()
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const [allbrands, setAllBrands] = useState();
  const [allsizes, setAllSizes] = useState();
  useEffect(() => {
    axios
      .get("http://localhost:8000/brands")
      .then((response) => {
        setAllBrands(response.data)
      })
      .catch(function (error) {
        console.log(error);
      });
  }, []);

  useEffect(() => {
    axios
      .get("http://localhost:8000/size")
      .then((response) => {
        setAllSizes(response.data)
      })
      .catch(function (error) {
        console.log(error);
      });
  }, []);

  const [title, setTitle] = useState();
  const [description, setDescription] = useState();
  const [price, setPrice] = useState();
  const [rating, setRating] = useState();
  // const [thumbnail, setThumbnail] = useState(null);
  const [brand, setBrand] = useState();
  const [sizes, setSizes] = useState();


  const handleok = (data) => {
    
    axios.post("http://localhost:8000/products/update/" + id, data).catch((error) => {
      console.log("Error in post data" + error.message);
     
    });
    toast("products updated successfully")
    setTimeout(() => {
      navigate("/dashbord");
    }, 6000);

  };

  useEffect(() => {
    axios
      .get(`http://localhost:8000/products-update/${id}`)
      .then((response) => {
        setTitle(response.data.title);
        setDescription(response.data.description);
        setPrice(response.data.price);
        setRating(response.data.rating);
        setBrand(response.data.brand);
        setSizes(response.data.sizes);

      })
      .catch((error) => {
        console.log("Error in fetching data: " + error.message);
      });


  }, [id])

  return (
    <div className="py-4 admin-detail">
      <div className="container px-5">
        <h2 className="py-3 color">Update Products</h2>
        <form
          className="row g-3"
          onSubmit={handleSubmit((data) => handleok(data))}
          encType="multipart/form-data"
        >
          <div className="col-12">
            <label for="title" className="form-label">
              Title
            </label>
            <input
              type="text"
              value={title}
              className="form-control formshadow"
              {...register("title", { required: "Title must be required" })}
              onChange={(e) => setTitle(e.target.value)}
            />

          </div>
          <div className="col-12">
            <label for="description" className="form-label">
              Description
            </label>
            <div className="mt-2">
              <textarea
                className="form-control formshadow"
                value={description}
                rows={3}
                {...register("description", {
                  required: "Description must be required",
                })}
                onChange={(e) => setDescription(e.target.value)}
              />

            </div>
          </div>
          <div className="col-lg-6">
            <label for="price" className="form-label">
              Price
            </label>
            <input
              type="number"
              value={price}
              className="form-control formshadow"
              {...register("price", { required: "Price must be required" })}
              onChange={(e) => setPrice(e.target.value)}
            />

          </div>
          <div className="col-lg-6">
            <label for="rating" className="form-label">
              Rating
            </label>
            <input
              type="number"
              value={rating}
              className="form-control  formshadow"
              {...register("rating", { required: "Rating must be required" })}
              onChange={(e) => setRating(e.target.value)}
            />

          </div>
          <div className="col-lg-6">
            <label for="description" className="form-label">
              Brand
            </label>
            <div className="mt-2">
              <select
                value={brand}
                className="form-select  formshadow"
                aria-label="Default select example"
                {...register("brand", { required: "Brand must be required" })}
                onChange={e => setBrand(e.target.value)}
              >
                <option selected>Open this select menu</option>
                {allbrands && allbrands.map((brand) => (
                  <option value={brand.value}>{brand.label}</option>
                ))}
              </select>

            </div>
          </div>
          <div className="col-lg-6">
            <label for="description" className="form-label">
              Size
            </label>
            <div className="mt-2">
              <select
                value={sizes}
                className="form-select  formshadow"
                aria-label="Default select example"
                {...register("size", { required: "Size must be required" })}
                onChange={(e) => setSizes(e.target.value)}
              >
                <option selected>Open this select menu</option>
                {allsizes && allsizes.map((sizes) => (
                  <option key={sizes.id} value={sizes.value}>
                    {sizes.label}
                  </option>
                ))}
              </select>

            </div>
          </div>

          <div className="col-lg-12 text-center d-grid py-5 pb-5">
            <button type="submit" className="btn mybtn btnoutlined py-2 btnadmin shadow ">
              Submit
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default AdminProductUpdate;

