import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, Navigate, useParams } from 'react-router-dom';
import { addqty, deleteItemFromCartAsync, selectCartItem, subqty } from './cartSlice';
import cartflw from '../../images/flower3.png'
import emptycard1 from '../../images/emptycard2.png'

const Cart = () => {
    const dispatch = useDispatch();
    const { id } = useParams();
    const [sizes, setSizes] = useState(1);
    const handleSize = (sizes) => {
        var size = sizes.slice(0, -2);
        size = size / 100;
        setSizes(size);

    }
    const [quantity, setQuantity] = useState(0);

    const minus = (data) => {
        dispatch(subqty(data))

    }
    const plus = (data) => {
        dispatch(addqty(data))
    }

    const cartdata = useSelector(selectCartItem)


    var Totalprice = 0;
    cartdata.map((item) => {
        Totalprice = Totalprice + item.price * item.quantity;
    })

    const totalQuantity = () => {
        let totalqty = 0
        cartdata.map((item) => {
            totalqty = totalqty + item.quantity;
        })
        setQuantity(totalqty)
    }
    useEffect(() => {
        totalQuantity()
    }, [cartdata])

    const handledelete = (id) => {
        dispatch(deleteItemFromCartAsync(id));
    }

    return (
        <>
            {/* {cartdata && cartdata.length <= 0 && <Navigate to={'/'} replace={true}></Navigate>} */}
            {!cartdata.length && cartdata ?
                <>
                    <p className='py-5 mt-5 text-center'></p>
                    <div className='container'>
                        <div className='row'>
                            <div className='col-lg-5 px-5 m-auto shadow-lg py-5 homebg rounded-4'>

                                <h1 className='extradetail text-center fw-bold'>Shop Quicker</h1>
                                <p className='text-center tblhead'>Your Cart is empty. Start shopping now!</p>
                                <div className='d-flex align-items-center py-4 justify-content-center'>
                                    <img src={emptycard1} alt="" width={300} />
                                </div>
                                <div className='d-flex justify-content-center homebtn w-100'>
                                    <Link to='/Products' className='btn myfont fw-bold mybtn btnoutlined w-100'>
                                        Shop Now
                                        <span className='ps-1 homeicon'><i className="fa-solid fa-angle-right"></i></span>
                                    </Link>
                                </div>
                               
                            </div>
                        </div>

                    </div>
                </> :
                <div className='container pt-5 mt-5'>
                    <div className='row position-relative'>
                        <div className='col-lg-8 pb-4 pt-2'>
                            <div className='table-responsive px-3'>
                                <h2 className='ps-lg-5 hometitle text-lg-start text-center'>Cart</h2>
                                <table className='table table-borderless align-middle'>
                                    <thead className='' >
                                        <tr className='myfont footer '>
                                            <th className='pt-3 text-lg-center tblhead'>Product</th>
                                            <th className='pt-3 tblhead'>Details</th>
                                            <th className='pt-3 tblhead'>Price</th>
                                            <th className='pt-3 tblhead ps-lg-4'>Count</th>
                                            <th className='pt-3 tblhead'>Total</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {cartdata.map((data, index) =>
                                            <>
                                                <tr><td className='bg-transparent py-2'></td></tr>
                                                <tr className='shadow rounded rounded-4'>
                                                    <td className='rounded-4 rounded-end-0  myfont extradetail d-flex justify-content-center' style={{ marginBottom: "10px" }}>
                                                        <img src={require(`../../uploads/${data.product.thumbnail}`)} alt={data.product.title} className="" height={100} />
                                                    </td>
                                                    <td className='pt-4  extradetail'>
                                                        <p>{data.product.title}</p>
                                                        <p className='text-secondary'>Size:{data.sizes[0]}</p>
                                                    </td>
                                                    <td className='py-4 myfont extradetail'> {data.price}Rs.</td>
                                                    <td className='py-4 myfont'>
                                                        <Link onClick={() => minus(data)} className='pe-3 fs-4 qty'><i class="bi bi-patch-minus"></i></Link>
                                                        <span className='fs-4 text-secondary px-1 pe-lg-0 pe-md-0 pe-5'>{data.quantity}</span>
                                                        <Link onClick={() => plus(data)} className='px-lg-3 px-md-3 fs-4 qty'><i class="bi bi-patch-plus"></i></Link>

                                                    </td>
                                                    <td className='py-4 myfont extradetail'>
                                                        {data.quantity * data.price}Rs.
                                                    </td>
                                                    <td className='py-4 myfont extradetail rounded-4 rounded-start-0'><button className='btn border-0 text-danger  fs-4'><i class="fa-solid fa-xmark" onClick={() => handledelete(data.id)}></i></button></td>
                                                </tr>
                                            </>
                                        )}

                                    </tbody>
                                </table>

                            </div>
                            <div className='homebg pt-5 mx-lg-3 my-4 pb-4 text-center rounded rounded-4 '>
                                <h3 className='extradetail '>Check The Newest Products</h3>
                                <p className='qty'>Continue to shopping...</p>

                                <div className='d-flex justify-content-center homebtn '>
                                    <Link to='/Products' className='btn myfont fw-bold mybtn btnoutlined '>Shop Now
                                        <span className='ps-1 homeicon '><i class="fa-solid fa-angle-right"></i></span>
                                    </Link>
                                </div>
                            </div>
                            <img src={cartflw} alt="" className='img-fluid position-absolute cartimg' />
                        </div>
                        <div className='col-lg-4 px-lg-5 py-5'>
                            <div className='homebg py-4 px-5 rounded rounded-4'>
                                <h3 className='extradetail py-3'>Order Summary</h3>
                                <div className='d-flex justify-content-between fs-5 tblhead'>
                                    <p>Subtotal</p>
                                    <p>{Math.ceil(Totalprice)} Rs.</p>
                                </div>
                                <div className='d-flex justify-content-between fs-5 tblhead'>
                                    <p>Total Items</p>
                                    <p>{quantity}</p>
                                </div>
                                <hr />
                                <div className='d-flex justify-content-between fs-5 tblhead'>
                                    <p>Total</p>
                                    <p>{Math.ceil(Totalprice)} Rs.</p>
                                </div>
                                <div className=" text-center d-grid py-3  homebtn">
                                    <Link to='/checkout' className='btn myfont fw-bold mybtn btnoutlined text-white '>
                                        Checkout
                                        <span className='ps-1 homeicon '><i class="fa-solid fa-angle-right"></i></span>
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            }

        </>
    )
}

export default Cart
