import axios from 'axios';
import React, { useEffect, useState } from 'react'

const ManageBrand = () => {
    const [allbrands, setAllBrands] = useState();
    const [brandsDelete, setBrandsDelete] = useState()

    useEffect(() => {
        axios
            .get("http://localhost:8000/brands")
            .then((response) => {
                setAllBrands(response.data)
            })
            .catch(function (error) {
                console.log(error);
            });
    }, [brandsDelete]);

    // const handleDelete = (value) => {
    //     console.log(value);

    //     axios.get(`http://localhost:8000/brands/${value}`)
    //         .then((response) => {
    //             setBrandsDelete(response.data);
    //         })
    //         .catch((error) => {
    //             console.log(error);
    //         });

    //     // axios.delete(`http://localhost:8000/brands/${id}`).then((response) => {
    //     //     setBrandsDelete(response)

    //     // }).catch(function (error) {
    //     //     console.log(error);
    //     // })
    // }

    const handleDelete = (value) => {
        console.log("Trying to delete brand:", value);

        // Step 1: Check if any products use this brand name
        axios.get(`http://localhost:8000/brands/${value}`)
            .then((response) => {
                const products = response.data;
                console.log(products);

                if (products.length > 0) {
                    alert(` Cannot delete "${value}" because it's used by products.`);
                    return;
                }
                console.log(value);

                if (window.confirm(` No products use "${value}". Delete this brand?`)) {
                    console.log("hello");
                    
                    axios.delete(`http://localhost:8000/brands/${value}`).then((response) => {
                        setBrandsDelete(response)

                    }).catch(function (error) {
                        console.log(error);
                    })
                }

            })
            .catch((error) => {
                console.log("Error checking products:", error);
            });
    };


    return (
        <>
            <div className="py-5 admin-detail">
                <div className="container  px-5">
                    <div className="row">
                        <div className="col-lg-8">
                            <h2 className="tablecolor text-lg-start text-center">LIST OF BRANDS...</h2>
                        </div>
                    </div>
                </div>
                <div className="container my-4 px-lg-5 table-responsive">
                    <table className="table table-borderless align-middle">
                        <thead className=" text-center">
                            <tr className="shadow rounded rounded-4">
                                <th className="tabletablehead py-4 myfont rounded-4 rounded-end-0 fw-semibold">Id</th>
                                <th className="tabletablehead py-4 myfont fw-semibold">Label </th>
                                <th className="tabletablehead py-4 myfont fw-semibold">Value </th>
                                <th className="tabletablehead py-4  myfont fw-semibold rounded-4 rounded-start-0">Action</th>
                            </tr>
                        </thead>
                        <tbody className="text-center ">

                            {allbrands && allbrands.map((brand) => (
                                <>
                                    <tr><td className='bg-transparent pb-3'></td></tr>
                                    <tr className="tableshedow  rounded rounded-4">
                                        <td className=" px-0 py-3  myfont tablecolor rounded-4 rounded-end-0">
                                            {brand.id}
                                        </td>
                                        <td className="py-4 tablecolor px-4"> {brand.label}</td>
                                        <td className="py-4 tablecolor px-4"> {brand.value}</td>
                                        <td className="py-4 tablecolor rounded-4 rounded-start-0">
                                            <button className="fs-5 text-danger bg-transparent border-0" onClick={() => {
                                                if (window.confirm("Are you sure you want to delete?")) {
                                                    handleDelete(brand.value);
                                                }
                                            }}>
                                                <i className="fa-solid fa-calendar-xmark"></i>
                                            </button>
                                        </td>
                                    </tr>
                                </>

                            ))}
                        </tbody>
                    </table>
                </div>
            </div>
        </>
    )
}

export default ManageBrand
