
import React, { useEffect } from 'react'
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import logo from '../../images/logo.png'
import test from '../../images/tes3-removebg-preview.png'
import { Link, Navigate, useNavigate } from 'react-router-dom';
import { loginUserAsync, selectUserInfo } from '../user/userSlice';
import { toast } from 'react-toastify';

const Reset = () => {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const user = useSelector(selectUserInfo)

    const err = useSelector(state => state.user.error)
    console.log(err);


    const {
        register,
        handleSubmit,
        reset,
        formState: { errors },

    } = useForm();


    const addtoemail = (data) => {
        
        navigate('/forgot/' + data.email)
    }


    if (user && user.role === 'admin') {
        return <Navigate to='/dashbord' replace={true}></Navigate>
    }
    return (
        <>
            {user && <Navigate to={'/'} replace={true}></Navigate>}
            <div className='container-fluid py-5 px-5 homebg '>
                <div className='container loginbg py-5 my-4 px-4 bg-white shadow-lg position-relative overflow-hidden'>
                    <div className='row'>
                        <div className='col-lg-5 m-auto '>
                            <h1 className=' pt-1 text-center'>
                                <img src={logo} alt="" height={60} />
                                <span className='loginlogo px-2'>Euphoria</span>
                            </h1>
                            <p className='text-center loginfont'>Fragrances fill the senses with the mysterious</p>
                            <h3 className='text-center loginfont pt-5 fw-semibold'>Send Email</h3>

                            {/* signup */}
                            <form onSubmit={handleSubmit((data) => addtoemail(data))}

                                className='py-4'>

                                <div class="form-floating  mb-3">
                                    <input type="email" class="form-control loginform" id="floatingInput" autocomplete="off" placeholder="name@example.com"                                       {...register("email", {
                                        required: "Email must be required",
                                    })}
                                    />
                                    <label for="email">Email</label>
                                    {errors.email && (
                                        <span className="text-danger pt-2">Email must be required.</span>
                                    )}
                                </div>


                                <div className='row mt-5'>
                                    <div className="col-lg-12  text-lg-center py-3 homebtn d-lg-grid d-block text-end">
                                        <button className='btn myfont fw-bold mybtn btnoutlined ' type='submit'>Send Email
                                            <span className='ps-1 homeicon '><i class="fa-solid fa-angle-right"></i></span>
                                        </button>
                                    </div>
                                </div>
                                <p className='text-center text-secondary'>Already have a no account? <Link className='text-decoration-none qty fw-bold' to='/signup'>SignUp</Link></p>
                            </form>

                            <img src={test} alt="" className='position-absolute loginimg' />
                            <img src={test} alt="" className='position-absolute loginimg1' />
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Reset

