import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { addreview, fetchAllReviews, fetchUserReviewById } from './reviewAPI';

const initialState = {
  value: 0,
  review: []
};
export const addreviewAsync = createAsyncThunk(
  'review/addreview',
  async (item) => {
    console.log(item);

    const response = await addreview(item);
    return response.data;
  }
);

export const fetchUserReviewByIdAsync = createAsyncThunk(
  'review/fetchUserReviewById',
  async (id) => {
    const response = await fetchUserReviewById(id);
    return response.data;
  }
);

export const fetchAllReviewsAsync = createAsyncThunk(
  'order/fetchAllReviews',
  async () => {
    const response = await fetchAllReviews();

    return response.data;
  }
);

export const reviewSlice = createSlice({
  name: 'review',
  initialState,

  reducers: {

  },
  extraReducers: (builder) => {
    builder
      .addCase(addreviewAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(addreviewAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.review = action.payload;
      })
      .addCase(fetchUserReviewByIdAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchUserReviewByIdAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.review = action.payload;
      }).addCase(fetchAllReviewsAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllReviewsAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.review = action.payload;
      });
  },
});

export const selectReviews = (state) => state.review.review;

export default reviewSlice.reducer;
