import React, { useEffect } from 'react'
import upflower from '../../images/success.png';
import { Link, Navigate, useParams } from 'react-router-dom';
import { resetCartAsync } from '../cart/cartSlice';
import { resetOrder } from '../order/orderSlice';
import { useDispatch, useSelector } from 'react-redux';
import { selectUserInfo } from '../user/userSlice';


const OrderSuccess = () => {
    const params = useParams()
    const dispatch = useDispatch();
    const user = useSelector(selectUserInfo)

    useEffect(() => {
        dispatch(resetCartAsync(user))
        dispatch(resetOrder())
    }, [dispatch,user])
    return (
        <>
            {!params.id && <Navigate to='/' replace={true}></Navigate>}
            <div className='container-fluid '>
                <div className='container'>
                    <div className='row'>
                        <div className='col-lg-8 m-auto position-relative '>
                            <img src={upflower} className='success position-absolute ' alt="" />
                            <img src={upflower} alt="" className='success1 position-absolute' />
                            <div className=' poster rounded rounded-4 position-absolute text-center z-1 pt-3 pb-4'>
                                <h1 className='order fw-bold mb-0'>Order Success</h1>
                                <p className='p-0  tblhead'>OrderId : {params?.id}</p>
                                <Link to={'/'} class=" btn px-5  pcardsuccess fw-bold py-2 rounded-4">
                                    Go to Home
                                </Link>

                            </div>
                            <img src={upflower} className='success2 position-absolute' alt="" />
                            <img src={upflower} alt="" className='success3 position-absolute' />


                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default OrderSuccess
