import axios from 'axios';
import React, { useState } from 'react'
import { useForm } from 'react-hook-form';
import { useNavigate } from 'react-router-dom';
import per from "../images/per.png"
import { toast } from 'react-toastify';

const AdminBrands = () => {
  const navigate = useNavigate()
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  const handleok = (data) => {
    console.log(data);

    axios.post("http://localhost:8000/brands", data)
      .then((response) => {
        console.log("Brand created successfully:", response.data);
      })
      .catch((error) => {
        console.log("Error in post data:", error.message);
      });

    toast("Brand Added successfully")
    setTimeout(() => {
      navigate("/dashbord");
    }, 4000);
  }
  return (
    <>
      <div className='container  py-5'>
        <h2 className='pt-5 px-4 color py-4'>+Including New Brands...</h2>
        <div className="row pt-4">
          <div className='col-lg-6 px-lg-5'>

            <p className='font tablecolor'>A brand for a company is like a reputation for a person. You earn reputation by trying to do hard things well." "Branding is the art of aligning what you want people to think about your company with what people actually do think about your company. And vice-versa.</p>

            <form onSubmit={handleSubmit((data) => handleok(data))}>
              <div className='row'>
                <div className="col-12 py-4">
                  <label for="brand" className="form-label color">
                    *Brand Name
                  </label>
                  <input
                    type="text"
                    className="form-control formshadow"
                    {...register("label", { required: "Brand must be required" })}
                  />
                  {errors.label && (
                    <p className="text-danger pt-2">Brand must be required.</p>
                  )}
                </div>
                <div className="col-12">
                  <label for="brand" className="form-label color">
                    *Brand Value
                  </label>
                  <input
                    type="text"
                    className="form-control formshadow"
                    {...register("value", { required: "Brand must be required" })}
                  />
                  {errors.value && (
                    <p className="text-danger pt-2">Brand must be required.</p>
                  )}
                </div>
                <div className="col-lg-12 text-center d-grid py-5">
                  <button type="submit" className="btn mybtn btnoutlined py-2 btnadmin shadow ">
                    Submit
                  </button>
                </div>
              </div>
            </form>
          </div>
          <div className='col-lg-6 '>
            <img src={per} alt="" className='img-fluid per ' />
          </div>
        </div>
      </div>
    </>
  )
}

export default AdminBrands
