import React from 'react'
import logo from '../images/logo.png'
import { Link } from 'react-router-dom'
import { useSelector } from 'react-redux'
import { selectCartItem } from '../features/cart/cartSlice'
import { selectUserInfo } from '../features/user/userSlice'

const Navbar = ({ children }) => {
    const cartdata = useSelector(selectCartItem)
    const user = useSelector(selectUserInfo)

    return (
        <>
            <div className='container-fluid  nevbarhome  p-0 fixed-top '>
                <nav class="navbar navbar-expand-lg bg-body-tertiary">
                    <div class="container">
                        <Link class="navbar-brand d-flex justify-content-center align-items-center" to='/Products'>
                            <img src={logo} alt="" height={50} />
                            <span className='logofont px-2'>Euphoria</span>
                        </Link>

                        <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span class="navbar-toggler-icon"></span>
                        </button>
                        <div class="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul class="navbar-nav m-auto mb-2 mb-lg-0">
                                <li class="nav-item px-3">
                                    <Link class="nav-link" aria-current="page" to='/'>Home</Link>
                                </li>
                                <li class="nav-item px-3">
                                    <Link class="nav-link " to='/Products'>Products</Link>
                                </li>
                                <li class="nav-item px-3">
                                    <Link class="nav-link " to='/About'>About</Link>
                                </li>
                                <li class="nav-item px-3">
                                    <Link class="nav-link " to='/Blog'>Blog</Link>
                                </li>
                                <li class="nav-item px-3">
                                    <Link class="nav-link " to='/trackOrder'>Order</Link>
                                </li>

                            </ul>
                            <div className='fs-5 d-flex justify-content-center align-items-center'>

                                <div>
                                    <Link to={'/wishlist'} className='px-2 iconcolor'><i class="fa-solid fa-heart"></i></Link>
                                </div>
                                <div>
                                    <Link to='/cart' className='px-3 iconcolor position-relative'><i class="fa-solid fa-cart-shopping"></i>
                                        <p class="position-absolute top-0 start-75 translate-middle badge rounded-pill mybadge">
                                            {cartdata && cartdata.length}
                                        </p>

                                    </Link>
                                </div>
                                <div className='dropdown'>

                                    <Link class="px-2 iconcolor " to='#' role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                        <i class="fa-solid fa-user"></i>
                                    </Link>
                                    <ul class="dropdown-menu">
                                        <li><Link class="dropdown-item" to='/userprofile'>Profile</Link></li>
                                        <li><Link class="dropdown-item" to='/user-order'>Orders</Link></li>
                                        {user && user.email ? (
                                            <>
                                                <li><hr className="dropdown-divider" /></li>
                                                <li><Link className="dropdown-item" to='/logout'>Log Out</Link></li>
                                            </>
                                        ) : (
                                            <li><Link className="dropdown-item" to='/login'>Log In</Link></li>
                                        )}

                                    </ul>

                                </div>
                            </div>
                        </div>
                    </div>
                </nav>
            </div>
            {children}
        </>
    )
}

export default Navbar
