import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { fetchAllProduct, fetchBrands, fetchProductById } from './productAPI';

const initialState = {
  products: [],
  brands: [],
  status: "idle",
  selectedProduct: [],
};

export const fetchAllProductAsync = createAsyncThunk(
  'product/fetchAllProduct',
  async ({ selectbrand, sort }) => {
    const response = await fetchAllProduct(selectbrand, sort);
    return response.data;
  }
);

export const fetchProductByIdAsync = createAsyncThunk(
  'product/fetchProductById',
  async (id) => {
    const response = await fetchProductById(id);
    return response.data;
  }
);



export const fetchBrandsAsync = createAsyncThunk(
  'product/fetchBrands',
  async () => {
    const response = await fetchBrands();
    return response.data;
  }
);

export const productSlice = createSlice({
  name: 'product',
  initialState,
  reducers: {

    increment: (state, action) => {
      const { id } = action.payload
      const product = state.productDetails;

      if (product && product.id === id) {
        product.qty = product.qty + 1
      }
    },
    decrement: (state, action) => {
      const { id } = action.payload
      const product = state.productDetails;
      if (product && product.id === id && product.qty > 1) {
        product.qty = product.qty + 1
      }
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchAllProductAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllProductAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.products = action.payload;
      })
      .addCase(fetchBrandsAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchBrandsAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.brands = action.payload;
      })
      .addCase(fetchProductByIdAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchProductByIdAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.selectedProduct = action.payload;
      })
      
  },
});

export const selectAllProducts = (state) => state.product.products;
export const productDetails = (state) => state.product.selectedProduct;
export const selectBrands = (state) => state.product.brands;

export const { increment, decrement } = productSlice.actions;

export default productSlice.reducer;
