export function addToCart(item) {

  return new Promise(async (resolve) => {
    const response = await fetch("http://localhost:8000/cart", {
      method: "POST",
      body: JSON.stringify(item),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();
    console.log(data);


    resolve({ data });
  });
}

export function fetchItemsByUserId(user) {
  return new Promise(async (resolve) => {
    const response = await fetch('http://localhost:8000/cart/' + user.id);
    const data = await response.json();
    resolve({ data });
  });
}

export function deleteItemFromCart(itemId) {
  return new Promise(async (resolve) => {
    const response = await fetch('http://localhost:8000/cart/' + itemId, {
      method: 'DELETE',
      headers: { 'content-type': 'application/json' },
    });
    const data = await response.json();
    
    resolve({ data: { id: itemId } });
  });
}

export function resetCart(user) {
  return new Promise(async (resolve) => {
    const response = await fetchItemsByUserId(user);
    const items = response.data;
    for (let item of items) {
      await deleteItemFromCart(item.id);
    }
    resolve({ status: 'success' });
  });
}
