import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { addToCart, deleteItemFromCart, fetchItemsByUserId, resetCart } from './cartAPI';

const initialState = {
  cart: [],
  status: 'idle',
  cartLoaded: false
};

export const addToCartAsync = createAsyncThunk(
  'cart/addToCart',
  async (item) => {
    const response = await addToCart(item);
    return response.data;
  }
);
export const fetchItemsByUserIdAsync = createAsyncThunk(
  'cart/fetchItemsByUserId',
  async (user) => {
    const response = await fetchItemsByUserId(user);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const deleteItemFromCartAsync = createAsyncThunk(
  'cart/deleteItemFromCart',
  async (itemId) => {
    const response = await deleteItemFromCart(itemId);
    
    return response.data;
  }
);

export const resetCartAsync = createAsyncThunk(
  'cart/resetCart',
  async (user) => {
    const response = await resetCart(user);
    return response.data;
  }
);



export const cartSlice = createSlice({
  name: 'cart',
  initialState,
  reducers: {
    addqty: (state, action) => {
      
      const { id, quantity } = action.payload
      const item = state.cart.find((i) => i.id == id)

      if (item) {
        item.quantity = quantity + 1
      }
    },
    subqty: (state, action) => {
      const { id, quantity } = action.payload
      const item = state.cart.find((i) => i.id === id)
      if (item && item.quantity > 1) {
        item.quantity = quantity - 1
      }
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(addToCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(addToCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.cart.push(action.payload);
      })
      .addCase(fetchItemsByUserIdAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchItemsByUserIdAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.cart = action.payload;
      })
      .addCase(deleteItemFromCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(deleteItemFromCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index =  state.cart.findIndex(item=>item.id===action.payload.id)
        state.cart.splice(index,1);
      })
      .addCase(resetCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(resetCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.cart = [];
      });
  },
});

export const { addqty, subqty } = cartSlice.actions;

export const selectCartItem = (state) => state.cart.cart;

export default cartSlice.reducer;
