import React, { useEffect, useState } from 'react'
import heart from '../../images/heart.png'
import wisflower from '../../images/tes3-removebg-preview.png'
import { useDispatch, useSelector } from 'react-redux'
import { selectUserInfo } from '../user/userSlice'
import { deleteFromWishAsync, fetchWishByUserAsync, selectWishItem } from './wishSlice'
import { Link, useNavigate } from 'react-router-dom'

const Wishlist = () => {
    const dispatch = useDispatch();
    const nevigate = useNavigate();
    const user = useSelector(selectUserInfo)
    const WishItem = useSelector(selectWishItem)


    useEffect(() => {
        dispatch(fetchWishByUserAsync(user))
    }, [user, dispatch])

    const handledelete = (id) => {
        dispatch(deleteFromWishAsync(id));
    }

    return (

        <>
            {!WishItem.length && WishItem ? <>
                <p className='py-5 mt-5 text-center'></p>
                <div className='container'>
                    <div className='row'>
                        <div className='col-lg-5 px-5 m-auto shadow-lg py-5 homebg rounded-4'>

                            <h1 className='extradetail text-center fw-bold'>Add Wish Quicker</h1>
                            <p className='text-center tblhead'>Your Wish is empty. Add to wishlist now!</p>
                            <div className='d-flex py-4 align-items-center justify-content-center'>
                                <img src={heart} alt="" width={200} />
                            </div>
                            <div className='d-flex justify-content-center homebtn w-100'>
                                <Link to='/Products' className='btn myfont fw-bold mybtn btnoutlined w-100'>
                                    Shop Now
                                    <span className='ps-1 homeicon'><i className="fa-solid fa-angle-right"></i></span>
                                </Link>
                            </div>

                        </div>
                    </div>

                </div>
            </> :
                <div className='container-fluid'>
                    <div className='container text-center py-5 position-relative'>
                        <p className='footer'>Your Perfect Wishlist</p>
                        <div className='display-3 aboutfont1'><i class="fa-regular fa-heart"></i></div>
                        <h1 className='hometitle'>Crave Collection</h1>
                        <hr />
                        <img src={wisflower} className="position-absolute mywishflw" />
                    </div>
                    <div className='container '>
                        <div className='table-responsive px-3'>
                            <table className="table table-borderless align-middle">
                                <thead className=" text-center">
                                    <tr className="shadow rounded rounded-4">
                                        <th className="tabletablehead1 py-4 myfont rounded-4 rounded-end-0 fw-semibold">Product</th>
                                        <th className="tabletablehead1 py-4 myfont fw-semibold">Title</th>
                                        <th className="tabletablehead1 py-4 myfont fw-semibold">Price</th>
                                        <th className="tabletablehead1 py-4 myfont fw-semibold">Stock</th>
                                        <th className="tabletablehead1 py-4 myfont fw-semibold">Add to cart</th>
                                        <th className="tabletablehead1 py-4 myfont fw-semibold rounded-4 rounded-start-0" >Cancel</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {WishItem.map((wishdata) => (
                                        <>
                                            <tr><td className='bg-transparent py-2'></td></tr>
                                            <tr className='shadow rounded rounded-4 text-center'>
                                                <td className='rounded-4 rounded-end-0  myfont extradetail d-flex justify-content-center' style={{ marginBottom: "10px" }}>
                                                    <img src={require(`../../uploads/${wishdata.product.thumbnail}`)} className="" height={70} />
                                                </td>
                                                <td className='pt-4  extradetail'>
                                                    <p>{wishdata.product.title}</p>
                                                </td>
                                                <td className='py-4 myfont extradetail'> {wishdata.product.price} Rs.</td>
                                                <td className='py-4 myfont extradetail'> In stock</td>
                                                <td className='py-4 myfont extradetail bg-red'>
                                                    <Link
                                                        className="btn pcard fw-bold py-1 rounded-3"
                                                        type="button"
                                                        to={`/ProductDetails/${wishdata.product.id}`}
                                                    >
                                                        Add to cart<span className='px-2'><i class="fa-solid fa-bag-shopping"></i></span>
                                                    </Link>
                                                </td>
                                                <td className='py-4 myfont extradetail bg-red'>
                                                    <button
                                                        className="btn pcard fw-bold py-1 rounded-3"
                                                        type="button"
                                                        onClick={() => handledelete(wishdata.id)}
                                                    >
                                                        Delete <span className='px-2'><i className="fa-solid fa-xmark"></i></span>
                                                    </button>
                                                </td>


                                            </tr>
                                        </>
                                    ))}
                                </tbody>
                            </table>
                        </div>


                    </div>
                </div>
            }
        </>
    )
}

export default Wishlist
