import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";
import { fetchAllProductAsync, selectAllProducts } from "../features/product/productSlice";
import axios from "axios";
import { toast } from "react-toastify";

const AdminProductList = () => {
  const dispatch = useDispatch();
  const { id } = useParams()
  const [productDelete, setProductDelete] = useState()
  const [selectbrand, setSelectbrand] = useState(null)
  const [sort, setSort] = useState(null)

  useEffect(() => {
    dispatch(fetchAllProductAsync({ selectbrand, sort }))
  }, [dispatch, productDelete])

  const productdata = useSelector(selectAllProducts)


  const handleDelete = (id) => {
    axios.delete(`http://localhost:8000/product-delete/${id}`).then((response) => {
      setProductDelete(response)

    }).catch(function (error) {
      console.log(error);
    })
  }


  return (
    <div className="py-5 admin-detail">
      <div className="container  px-5">
        <div className="row">
          <div className="col-lg-8">
            <h2 className="tablecolor text-lg-start text-center">PRODUCTS INFORMATIONS...</h2>
          </div>
          <div className="col-lg-4">
            <div className="text-lg-end text-center">
              <Link
                className="mybtn btnoutlined"
                to="/insertproduct"
              >
                Add New Products
              </Link>
            </div>
          </div>
        </div>

      </div>
      <div className="container my-4 px-lg-5 table-responsive">
        <table className="table table-borderless align-middle">
          <thead className=" text-center">
            <tr className="shadow rounded rounded-4">
              <th className="tabletablehead py-4 myfont rounded-4 rounded-end-0 fw-semibold">Product</th>
              <th className="tabletablehead py-4 myfont fw-semibold">Name</th>
              <th className="tabletablehead py-4 myfont fw-semibold">Description</th>
              <th className="tabletablehead py-4 myfont fw-semibold">Price</th>
              <th className="tabletablehead py-4 myfont fw-semibold">Rating</th>
              <th className="tabletablehead py-4 myfont fw-semibold">Brands</th>
              <th className="tabletablehead py-4 myfont fw-semibold">Sizes</th>
              <th className="tabletablehead py-4 myfont fw-semibold rounded-4 rounded-start-0" colSpan={2}>Action</th>
            </tr>
          </thead>
          <br />
          <tbody className=" text-center">
            {productdata && productdata.map((data) => (
              <>
                <tr><td className='bg-transparent pb-3'></td></tr>
                <tr tr className="tableshedow  rounded rounded-4">
                  <td className="py-4 myfont tablecolor rounded-4 rounded-end-0">
                    <img src={require(`./../uploads/${data.thumbnail}`)} alt={data.title} className="rounded-3" width={60} height={60} />
                  </td>
                  <td className="py-4 px-0 tablecolor">{data.title}</td>
                  <td className="py-4 tablecolor px-2">{data.description}</td>
                  <td className="py-4 tablecolor">{data.price}$</td>
                  <td className="py-4 tablecolor">{data.rating}</td>
                  <td className="py-4 tablecolor">{data.brand}</td>
                  <td className="py-4 tablecolor">{data.sizes}</td>
                  <td className="py-4 tablecolor"><Link className="fs-5 text-info" to={`/product-update/${data.id}`}><i class="fa-solid fa-newspaper"></i></Link></td>
                  <td className="py-4 tablecolor rounded-4 rounded-start-0">
                    <button className="fs-5 text-danger bg-transparent border-0" onClick={() => {
                      if (window.confirm("Are you sure you want to delete?")) {
                        handleDelete(data.id);
                      }
                    }}>
                      <i className="fa-solid fa-calendar-xmark"></i>
                    </button>
                  </td>
                </tr>
              </>
            ))}


          </tbody>
        </table>
      </div>
    </div>
  );
};

export default AdminProductList;
