import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { activeUser, createUser, fetchAllUsers, fetchLoggedInUserOrders, loginUser, resetUser, updateUser } from './userAPI';

const initialState = {
  user: JSON.parse(localStorage.getItem('tokens')) || null,
  error: null,
  status: 'idle',
  userlist: []
};

export const createUserAsync = createAsyncThunk(
  'user/userdata',
  async (userdata) => {
    const response = await createUser(userdata);
    return response.data;
  }
);

export const loginUserAsync = createAsyncThunk(
  'user/loginUser',
  async (loginInfo, { rejectWithValue }) => {
    try {
      const response = await loginUser(loginInfo);
      return response.data;
    } catch (error) {
      console.log(error);
      return rejectWithValue(error);
    }
  }
);

export const updateUserAsync = createAsyncThunk(
  'user/updateUser',
  async (update) => {
    const response = await updateUser(update);
    return response.data;
  }
);


export const logoutUserAsync = createAsyncThunk(
  'user/logout',
  async () => {
    return null;
  }
);

export const resetUserAsync = createAsyncThunk(
  'user/resetUser',

  async (userdata) => {
    const response = await resetUser(userdata);
    return response.data;
  }
);

export const fetchAllUsersAsync = createAsyncThunk(
  'user/fetchAllUsers',

  async () => {
    const response = await fetchAllUsers();
    return response.data;
  }
);
export const activeUserAsync = createAsyncThunk(
  'user/activeUser',
  async (update) => {
    const response = await activeUser(update);
    return response.data;
  }
);


export const userSlice = createSlice({
  name: 'user',
  initialState,
  reducers: {

  },
  extraReducers: (builder) => {
    builder
      .addCase(createUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(createUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.user = action.payload;
      })
      .addCase(createUserAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.error = action.payload;
      })
      .addCase(loginUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(loginUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.user = action.payload;
        localStorage.setItem("tokens", JSON.stringify(action.payload))
      })
      .addCase(loginUserAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.error = action.payload;
      })
      .addCase(logoutUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.user = null;
      })
      .addCase(updateUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(updateUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.user = action.payload;
        localStorage.setItem("tokens", JSON.stringify(action.payload))
      })
      .addCase(fetchAllUsersAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllUsersAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.userlist = action.payload;
      })
      .addCase(activeUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(activeUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.user = action.payload;
        localStorage.setItem("tokens", JSON.stringify(action.payload))
      })
  },
});


export const selectUserInfo = (state) => state.user.user;
export const selectAdminUserList = (state) => state.user.userlist;
export const selectError = (state) => state.user.error.error;
export const selectAddress = (state) => state.user.user.addresses;



export default userSlice.reducer;
