import axios from "axios";
import React, { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

const InsertProduct = () => {
  const navigate = useNavigate()

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },

  } = useForm();

  const [allbrands, setAllBrands] = useState();
  const [allsizes, setAllSizes] = useState();
  useEffect(() => {
    axios
      .get("http://localhost:8000/brands")
      .then((response) => {
        setAllBrands(response.data)
      })
      .catch(function (error) {
        console.log(error);
      });
  }, []);

  useEffect(() => {
    axios
      .get("http://localhost:8000/size")
      .then((response) => {
        setAllSizes(response.data)
      })
      .catch(function (error) {
        console.log(error);
      });
  }, []);

  const [title, setTitle] = useState(null);
  const [description, setDescription] = useState(null);
  const [price, setPrice] = useState(null);
  const [rating, setRating] = useState(null);
  const [thumbnail, setThumbnail] = useState(null);
  const [brand, setBrand] = useState();
  const [sizes, setSizes] = useState();


  const handleok = () => {
    // e.preventDefault();
    const formData = new FormData();

    formData.append("title", title);
    formData.append("description", description);
    formData.append("price", price);
    formData.append("rating", rating);
    formData.append("brand", brand);
    formData.append("thumbnail", thumbnail);
    formData.append("sizes", sizes);


    axios.post("http://localhost:8000/products", formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    })
      .then(() => {
        toast("Product added successfully!");
        setTimeout(() => {
          navigate("/dashbord");
        }, 4000);
      })
      .catch((error) => {
        console.log("Error in post data: " + error.message);
        toast.error("Failed to add product!");
      });





  };
  return (
    <div className="pt-4 admin-detail">
      <div className="container px-lg-5 px-md-4">
        <h2 className="py-3 color text-lg-start text-center">Fetch New Products</h2>
        <form
          className="row g-3"
          onSubmit={handleSubmit((data) => handleok(data))}
          encType="multipart/form-data"
        >
          <div className="col-12">
            <label for="title" className="form-label">
              Title
            </label>
            <input
              type="text"
              className="form-control formshadow"
              {...register("title", { required: "Title must be required" })}
              onChange={(e) => setTitle(e.target.value)}
            />
            {errors.title && (
              <p className="text-danger pt-2">Title must be required.</p>
            )}
          </div>
          <div className="col-12">
            <label for="description" className="form-label">
              Description
            </label>
            <div className="mt-2">
              <textarea
                className="form-control formshadow"
                rows={3}
                {...register("description", {
                  required: "Description must be required",
                })}
                onChange={(e) => setDescription(e.target.value)}
              />
              {errors.description && (
                <p className="text-danger pt-2">
                  Description must be required.
                </p>
              )}
            </div>
          </div>
          <div className="col-lg-6">
            <label for="price" className="form-label">
              Price
            </label>
            <input
              type="number"
              className="form-control formshadow"
              {...register("price", { required: "Price must be required" })}
              onChange={(e) => setPrice(e.target.value)}
            />
            {errors.price && (
              <p className="text-danger pt-2">Price must be required.</p>
            )}
          </div>
          <div className="col-lg-6">
            <label for="rating" className="form-label">
              Rating
            </label>
            <input
              type="text"
              className="form-control  formshadow"
              {...register("rating", {
                required: "Rating must be required",
                min: {
                  value: 0,
                  message: "Min value is 0"
                },
                max: {
                  value: 5,
                  message: "Max value is 5"
                }
              })}
              onChange={(e) => setRating(e.target.value)}
              aria-invalid={errors.rating ? "true" : "false"}
            />
            {errors.rating && (
              <p className="text-danger pt-2">{errors.rating?.message}</p>
            )}
          </div>
          <div className="col-lg-6">
            <label for="description" className="form-label">
              Brand
            </label>
            <div className="mt-2">
              <select
                className="form-select  formshadow"
                aria-label="Default select example"
                {...register("brand", { required: "Brand must be required" })}
                onChange={e => setBrand(e.target.value)}
              >
                <option selected disabled value="">Open this select menu</option>
                {allbrands && allbrands.map((brand) => (
                  <option value={brand.value}>{brand.label}</option>
                ))}
              </select>
              {errors.brand && (
                <p className="text-danger pt-2">Brand must be required.</p>
              )}
            </div>
          </div>
          <div className="col-lg-6">
            <label for="description" className="form-label">
              Size
            </label>
            <div className="mt-2">
              <select
                className="form-select  formshadow"
                aria-label="Default select example"
                {...register("size", { required: "Size must be required" })}
                onChange={(e) => setSizes(e.target.value)}
              >
                <option selected disabled value="">Open this select menu</option>
                {allsizes && allsizes.map((sizes) => (
                  <option key={sizes.id} value={sizes.value}>
                    {sizes.label}
                  </option>
                ))}
              </select>
              {errors.size && (
                <p className="text-danger pt-2">Size must be required.</p>
              )}
            </div>
          </div>
          <div className="col-lg-12">
            <label for="title" className="form-label">
              Thumbnail
            </label>
            <input
              type="file"
              className="form-control  formshadow"
              id="thumbnail"
              {...register("thumbnail", {
                required: "Thumbnail must be required",
              })}
              onChange={(e) => setThumbnail(e.target.files[0])}
            />
            {errors.thumbnail && (
              <p className="text-danger pt-2">Thumbnail must be required.</p>
            )}
          </div>
          <div className="col-lg-12 text-center d-grid py-3 my-4">
            <button type="submit" className="btn mybtn btnoutlined py-2 btnadmin shadow ">
              Submit
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default InsertProduct;
