export function addToWish(item) {

    return new Promise(async (resolve) => {
      const response = await fetch("http://localhost:8000/wishlist", {
        method: "POST",
        body: JSON.stringify(item),
        headers: { "content-type": "application/json" },
      });
      const data = await response.json();
      console.log(data);
  
  
      resolve({ data });
    });
  }
  
  export function fetchWishByUser(user) {
    return new Promise(async (resolve) => {
      const response = await fetch('http://localhost:8000/wishlist/' + user.id);
      const data = await response.json();
      resolve({ data });
    });
  }
  
  export function deleteFromWish(itemId) {
    return new Promise(async (resolve) => {
      const response = await fetch('http://localhost:8000/wishlist/' + itemId, {
        method: 'DELETE',
        headers: { 'content-type': 'application/json' },
      });
      const data = await response.json();
      
      resolve({ data: { id: itemId } });
    });
  }
  
  export function resetWish(user) {
    return new Promise(async (resolve) => {
      const response = await fetchWishByUser(user);
      const items = response.data;
      for (let item of items) {
        await deleteFromWish(item.id);
      }
      resolve({ status: 'success' });
    });
  }
  