import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { fetchAllReviewsAsync, selectReviews } from '../features/review/reviewSlice';
import { Rating } from 'react-simple-star-rating';
import axios from 'axios';

const AdminReviews = () => {
    const dispatch = useDispatch()
    const reviewdata = useSelector(selectReviews)
    console.log(reviewdata);

    const [rating, setRating] = useState(0);
    const [reviewDelete, setReviewDelete] = useState()

    useEffect(() => {
        dispatch(fetchAllReviewsAsync());
    }, [dispatch, reviewDelete]);

    const handleRating = (rate) => {
        setRating(rate);
    };

    const handleDelete = (id) => {
        axios.delete(`http://localhost:8000/review/${id}`).then((response) => {
            setReviewDelete(response)
        }).catch(function (error) {
            console.log(error);
        })
    }


    const safeReviewData = Array.isArray(reviewdata) ? reviewdata : [];

    return (
        <>
            <div className="py-5 admin-detail">
                <div className="container  px-5">
                    <div className="row">
                        <div className="col-lg-8">
                            <h2 className="tablecolor text-lg-start text-center">CUSTOMER REVIEWS...</h2>
                        </div>
                    </div>
                </div>
                <div className="container my-4 px-lg-5 table-responsive">
                    <table className="table table-borderless align-middle">
                        <thead className=" text-center">
                            <tr className="shadow rounded rounded-4">
                                <th className="tabletablehead py-4 myfont rounded-4 rounded-end-0 fw-semibold">Index</th>
                                <th className="tabletablehead py-4 myfont fw-semibold">Email </th>
                                <th className="tabletablehead py-4 myfont fw-semibold">Description</th>
                                <th className="tabletablehead py-4 myfont fw-semibold">Rating</th>
                                <th className="tabletablehead py-4 myfont fw-semibold">Date</th>
                                <th className="tabletablehead py-4 myfont fw-semibold rounded-4 rounded-start-0">Action</th>
                            </tr>
                        </thead>
                        <tbody className=" text-center">
                            {safeReviewData.length > 0 ? (
                                safeReviewData.map((review, index) => (
                                    <>  <tr><td className='bg-transparent pb-3'></td></tr>
                                        <tr key={review.id} className="tableshedow  rounded rounded-4 ">
                                            <td className="py-4 px-0 tablecolor rounded-4 rounded-end-0 ">{index + 1}</td>
                                            <td className="py-4 tablecolor">{review.email}</td>
                                            <td className="py-4 tablecolor px-4">{review.review}</td>
                                            <td>
                                                <Rating
                                                    onClick={handleRating}
                                                    readonly={true}
                                                    initialValue={review.rating}
                                                    allowFraction={true}
                                                    size={25}
                                                    fillColor='rgb(233, 202, 81)'
                                                    emptyColor='rgb(203, 202, 198)'
                                                />
                                            </td>
                                            <td className="py-4 tablecolor">
                                                <span>{review.createdAt ? new Date(review.createdAt).toDateString() : null}</span>
                                            </td>
                                            <td className="py-4 tablecolor rounded-4 rounded-start-0">
                                                <button className="fs-5 text-danger bg-transparent border-0" onClick={() => handleDelete(review.id)}>
                                                    <i className="fa-solid fa-calendar-xmark"></i>
                                                </button>
                                            </td>
                                        </tr>
                                    </>
                                ))
                            ) : (
                                <tr>
                                    <td colSpan="6" className="text-center py-4">No reviews available</td>
                                </tr>
                            )}
                        </tbody>
                    </table>
                </div>
            </div>
        </>
    );
}

export default AdminReviews;
