import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { selectUserInfo, updateUserAsync } from '../user/userSlice';
import { useForm } from 'react-hook-form';
import cartflw from '../../images/flower3.png'

const UserProfile = () => {
    const { register, handleSubmit, formState: { errors }, setValue } = useForm();
    const dispatch = useDispatch();
    const user = useSelector(selectUserInfo);

    const [editingAddress, setEditingAddress] = useState(null);

    const handleAdd = () => {
        setEditingAddress('new'); 
        setValue("fname", "");
        setValue("address", "");
        setValue("city", "");
        setValue("state", "");
        setValue("pinCode", "");
        setValue("Phno", "");
    };

    const handleEditClick = (index) => {
        if (editingAddress === index) {
            setEditingAddress(null); 
        } else {
            setEditingAddress(index);  
            const address = user.addresses[index];
            setValue("fname", address.fname);
            setValue("address", address.address);
            setValue("city", address.city);
            setValue("state", address.state);
            setValue("pinCode", address.pinCode);
            setValue("Phno", address.Phno);
        }
    };

    const handleAddNewAddress = (data) => {
        const updatedUser = {
            ...user,
            addresses: [...user.addresses, data]
        };
        dispatch(updateUserAsync(updatedUser));
        setEditingAddress(null);
    };

    const handleEditAddress = (data, index) => {
        const updatedUser = {
            ...user,
            addresses: [...user.addresses]
        };
        updatedUser.addresses.splice(index, 1, data); 
        dispatch(updateUserAsync(updatedUser));
        setEditingAddress(null);  
    };

    const handleRemove = (e, index) => {
        const updatedUser = { ...user, addresses: [...user.addresses] };
        updatedUser.addresses.splice(index, 1); 
        dispatch(updateUserAsync(updatedUser));
    };

    return (
        <div className="container-fluid pt-5 mt-5">
            <div className="container position-relative pt-4">
                <h1 className="hometitle pt-3 ps-3">User Profile</h1>

                <div className="shadow-lg rounded-4 py-5 bg-white">
                    <p className="tblhead fw-bold h3 ps-5">User Name : {user.name}</p>
                    <p className="fw-semibold footer ps-5">Email : {user.email}</p>
                    <p className="fw-bold tblhead ps-5">Role : {user.role}</p>

                    <div className="ps-1 pe-5 d-flex justify-content-between">
                        <p className="fw-semibold fs-5 extradetail px-5">Your Addresses</p>
                        <button
                            className="btn pcard fw-bold py-2 mb-2 px-4 rounded-3"
                            type="button"
                            onClick={handleAdd}
                        >
                            + Add New Address
                        </button>
                    </div>

                    {(editingAddress === 'new' || editingAddress !== null) && (
                        <form
                            className="px-2 py-3"
                            onSubmit={handleSubmit(editingAddress === 'new' ? handleAddNewAddress : (data) => handleEditAddress(data, editingAddress))}
                        >
                            <div className="shadow-lg p-4 rounded-4">
                                <div className="pb-3">
                                    <label htmlFor="address" className="form-label tblhead">Name</label>
                                    <input
                                        type="text"
                                        className="form-control shadow"
                                        {...register("fname", { required: "Name is required" })}
                                    />
                                    {errors.fname && <p className="text-danger pt-2">Name must be required.</p>}
                                </div>
                                <div className="pb-3">
                                    <label htmlFor="address" className="form-label tblhead">Street Address</label>
                                    <input
                                        type="text"
                                        className="form-control shadow"
                                        {...register("address", { required: "Address is required" })}
                                    />
                                    {errors.address && <p className="text-danger pt-2">Address must be required.</p>}
                                </div>

                                <div className="row pb-3">
                                    <div className="col-4">
                                        <label htmlFor="city" className="form-label tblhead">City</label>
                                        <input
                                            type="text"
                                            className="form-control shadow"
                                            {...register("city", { required: "City is required" })}
                                        />
                                        {errors.city && <p className="text-danger pt-2">City must be required.</p>}
                                    </div>
                                    <div className="col-4">
                                        <label htmlFor="state" className="form-label tblhead">State / Province</label>
                                        <input
                                            type="text"
                                            className="form-control shadow"
                                            {...register("state", { required: "State is required" })}
                                        />
                                        {errors.state && <p className="text-danger pt-2">State must be required.</p>}
                                    </div>
                                    <div className="col-4">
                                        <label htmlFor="pinCode" className="form-label tblhead">ZIP / Postal Code</label>
                                        <input
                                            type="text"
                                            className="form-control shadow"
                                            {...register("pinCode", { required: "PinCode is required" })}
                                        />
                                        {errors.pinCode && <p className="text-danger pt-2">PinCode must be required.</p>}
                                    </div>
                                </div>

                                <div className="pb-3">
                                    <label htmlFor="Phno" className="form-label tblhead">Phone Number</label>
                                    <input
                                        type="text"
                                        className="form-control shadow"
                                        {...register("Phno", { required: "Phone number is required" })}
                                    />
                                    {errors.Phno && <p className="text-danger pt-2">Phone number must be required.</p>}
                                </div>

                                <div className="row pt-3">
                                    <div className="col-6 d-grid m-auto">
                                        <button
                                            className="btn pcard fw-bold py-2 rounded-3"
                                            type="submit"
                                        >
                                            {editingAddress === 'new' ? 'Add Address' : 'Update Address'}
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </form>
                    )}

                    {user && user.addresses.map((data, index) => (
                        <div className="px-5 my-2" key={index}>
                            <div className="border rounded-4 border-2 px-3 pt-3 pb-2">
                            <p className="tblhead fw-bold">{data.fname}</p>
                                <div className="d-flex justify-content-between">
                                    <p className="text-secondary">{data.address}</p>
                                    <button
                                        className="btn pcard border-0 fw-bold py-1 rounded-3"
                                        onClick={() => handleEditClick(index)}
                                    >
                                        Edit
                                    </button>
                                </div>
                                <div className="d-flex justify-content-between">
                                    <p className="text-secondary">City: {data.city} [{data.pinCode}]</p>
                                    <button className="btn pcard border-0 fw-bold py-1 rounded-3" onClick={(e) => handleRemove(e, index)}>
                                        Remove
                                    </button>
                                </div>
                                <p className="tblhead">Phone: {data.Phno}</p>
                            </div>
                        </div>
                    ))}
                </div>
                <img src={cartflw} alt="" className='img-fluid position-absolute profileimg' />
            </div>
        </div>
    );
};

export default UserProfile;
