import "./App.css";
import "../node_modules/bootstrap/dist/css/bootstrap.css";
import "../node_modules/bootstrap/dist/js/bootstrap.bundle.js"
import "bootstrap-icons/font/bootstrap-icons.css"; //for bootstrape icon
import "@fortawesome/fontawesome-free/css/all.min.css";//for font awesome icon
import { BrowserRouter, Routes, Route } from "react-router-dom";
import InsertProduct from "./Admin_panel/InsertProduct.js";
import AdminHome from "./Admin_panel/AdminHome.js";
import AdminProductList from "./Admin_panel/AdminProductList.js";
import Dashboard from "./Admin_panel/Dashbord.js";
import AdminProductUpdate from "./Admin_panel/AdminProductUpdate.js";
import AdminBrands from "./Admin_panel/AdminBrands.js";
import AdminSizes from "./Admin_panel/AdminSizes.js";
import { ToastContainer } from "react-toastify";
import Navbar from "./app/Navbar.js";
import HomePage from "./features/pages/HomePage.js";
import Product from "./features/product/Product.js";
import ProductDetails from "./features/product/ProductDetails.js";
import SignUpPage from "./features/pages/SignUpPage.js";
import LogInPage from "./features/pages/LogInPage.js";
import LogOutPage from "./features/pages/LogOutPage.js";
import Cart from "./features/cart/Cart.js";
import { useDispatch, useSelector } from "react-redux";
import { selectUserInfo } from "./features/user/userSlice.js";
import { fetchItemsByUserIdAsync } from "./features/cart/cartSlice.js";
import { useEffect } from "react";
import CheckoutPage from "./features/pages/CheckoutPage.js";
import OrderSuccess from "./features/pages/OrderSuccess.js";
import NotFound from "./features/pages/NotFound.js";
import StripeCheckout from "./features/pages/StripeCheckout.js";
import UserOrders from "./features/pages/UserOrders.js";
import UserProfile from "./features/pages/UserProfile.js";
import ContectPage from "./features/pages/Blog.js";
import AdminOrder from "./Admin_panel/AdminOrder.js";
import AboutPage from "./features/pages/AboutPage.js";
import Blog from "./features/pages/Blog.js";
import ForgetPassword from "./features/pages/ForgetPassword.js";
import Reset from "./features/pages/Reset.js";
import Wishlist from "./features/Wishlist/Wishlist.js";
import ProtectedAdmin from "./features/pages/ProtectedAdmin.js";
import Protected from "./features/pages/Protected.js";
import AdminReviews from "./Admin_panel/AdminReviews.js";
import AdminUserDetails from "./Admin_panel/AdminUserDetails.js";
import CompleteOrder from "./Admin_panel/CompleteOrder.js";
import ManageBrand from "./Admin_panel/ManageBrand.js";
import TrackOrder from "./features/pages/TrackOrder.js";


function App() {
  const user = useSelector(selectUserInfo)
  const dispatch = useDispatch()
  console.log(user);
  useEffect(() => {
    if (user) {
      dispatch(fetchItemsByUserIdAsync(user))
    }
  }, [dispatch, user]);

  return (
    <BrowserRouter>
      <Routes>
        <Route path="/dashbord" element={<ProtectedAdmin> <Dashboard><AdminHome /></Dashboard></ProtectedAdmin>}></Route>
        <Route path="/insertproduct" element={<Dashboard> <InsertProduct /></Dashboard>}></Route>
        <Route path="/manageProducts" element={<Dashboard><AdminProductList /></Dashboard>}></Route>
        <Route path="/product-update/:id" element={<Dashboard><AdminProductUpdate /></Dashboard>}></Route>
        <Route path="/addBrands" element={<Dashboard><AdminBrands /></Dashboard>}></Route>
        <Route path="/addSizes" element={<Dashboard><AdminSizes /></Dashboard>}></Route>
        <Route path="/adminorders" element={<Dashboard><AdminOrder></AdminOrder></Dashboard>}></Route>
        <Route path="/manageBreand" element={<Dashboard><ManageBrand></ManageBrand></Dashboard>}></Route>
        <Route path="/completeOrder" element={<Dashboard><CompleteOrder></CompleteOrder></Dashboard>}></Route>
        <Route path="/adminReviews" element={<Dashboard><AdminReviews></AdminReviews></Dashboard>}></Route>
        <Route path="/userDetails" element={<Dashboard><AdminUserDetails></AdminUserDetails></Dashboard>}></Route>
        <Route path="/" element={<Navbar><HomePage /></Navbar>}></Route>
        <Route path="/Products" element={<Navbar><Product /></Navbar>}></Route>
        <Route path="/About" element={<Navbar><AboutPage /></Navbar>}></Route>
        <Route path="/Blog" element={<Navbar><Blog></Blog></Navbar>}></Route>
        <Route path="/ProductDetails/:id" element={<Navbar><ProductDetails /></Navbar>}></Route>
        <Route path="/signup" element={<SignUpPage></SignUpPage>}></Route>
        <Route path="/login" element={<LogInPage></LogInPage>}></Route>
        <Route path="/logout" element={<LogOutPage></LogOutPage>}></Route>
        <Route path="/wishlist" element={<Navbar><Wishlist></Wishlist></Navbar>}></Route>
        <Route path="/cart" element={<Protected><Navbar><Cart></Cart></Navbar></Protected>}></Route>
        <Route path="/checkout" element={<Protected><Navbar><CheckoutPage></CheckoutPage></Navbar></Protected>}></Route>
        <Route path="/reset" element={<Reset></Reset>}></Route>
        <Route path="/forgot/:email" element={<ForgetPassword></ForgetPassword>}></Route>
        <Route path="/ordersuccess/:id" element={<OrderSuccess></OrderSuccess>}></Route>
        <Route path="/stripe-checkout" element={<StripeCheckout></StripeCheckout>}></Route>
        <Route path="/trackOrder" element={<Protected><Navbar><TrackOrder></TrackOrder></Navbar></Protected>}></Route>
        <Route path="/user-order" element={<Navbar><UserOrders></UserOrders></Navbar>}></Route>
        <Route path="/userprofile" element={<Navbar><UserProfile></UserProfile></Navbar>}></Route>
        <Route path="/error" element={<Navbar><NotFound></NotFound></Navbar>}></Route>
      </Routes>
      <ToastContainer
        position="top-center"
        autoClose={3000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick={false}
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
    </BrowserRouter>
  );
}

export default App;
